import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ConfigureService } from '../config/configure/configure.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  constructor(private http: HttpClient) {}

  /**
   * getToken
   */
  public getToken() {
    const api = 'api/auth/get-token';
    const hotel = ConfigureService.config.hotel;

    return this.http.post(api, {
      client_id: hotel.client_id,
      client_secret: hotel.client_secret,
      grant_type: hotel.grant_type,
      scope: hotel.scope,
    });
  }
}
