import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { CHECK_IN_MODES } from 'src/app/shared/constant/check-mode';
import { ACTIONS } from 'src/app/shared/constant/event';
import { PAGES, STEPS } from 'src/app/shared/constant/pages';
import { HubModel } from 'src/app/shared/models/hub.models';
import { HubWalkinService } from 'src/app/shared/services/hub-walkin/hub-walkin.service';
import { HubService } from 'src/app/shared/services/hub/hub.service';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss'],
})
export class MenuComponent implements OnInit, OnDestroy {
  public modes = CHECK_IN_MODES;

  public listEntry = [
    {
      mode: this.modes.BOOKING_CODE,
      head: 'menu.checkin-booking-code.head',
      sub: 'menu.checkin-booking-code.sub',
      icon: './assets/images/booking-code.svg',
    },
    {
      mode: this.modes.QR_CODE,
      head: 'menu.checkin-qr.head',
      sub: 'menu.checkin-qr.sub',
      icon: './assets/images/qr.svg',
    },
    {
      mode: this.modes.WALK_IN,
      head: 'menu.booking.head',
      sub: 'menu.booking.sub',
      icon: './assets/images/booking.svg',
    },
  ];

  public selectedMode: string = '';
  private hubSubcription = null;

  constructor(private router: Router, private hubService: HubService, private translateService: TranslateService, private hubWalkin: HubWalkinService) {
    this.hubSubcription = this.hubService.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.options) {
        return;
      }
      console.log('FLOW-CHECKIN: ', res);
      if (res.message === ACTIONS.goBack) {
        this.router.navigateByUrl('/');
      }
    });
  }

  ngOnInit(): void {}

  ngOnDestroy(): void {
    this.hubSubcription.unsubscribe();
  }

  selectMode(mode: string): void {
    this.selectedMode = mode;
  }

  redirect(): void {
    let mode = this.selectedMode;

    if (!mode) {
      this.hubService.error(this.translateService.instant('msg.notSelectOption'));
      return;
    }

    if (mode === this.modes.WALK_IN) {
      // this.router.navigateByUrl(`/${PAGES.flowBooking}`);
      this.router.navigate([`/${PAGES.walkin}/${STEPS.step1}`]);
      this.hubWalkin.setPage(PAGES.walkin);
      return;
    }
    if (mode === this.modes.QR_CODE) {
      // this.router.navigateByUrl(`/${PAGES.flowBooking}`);
      this.router.navigate([`/${PAGES.checkinQr}/${STEPS.step1}`]);
      this.hubWalkin.setPage(PAGES.checkinQr);
      return;
    }
    this.router.navigate([`/${PAGES.checkinCode}/${STEPS.step1}`]);
    // this.router.navigate([`/${PAGES.flowCheckin}`], { queryParams: { mode } });
    this.hubWalkin.setPage(PAGES.checkinCode);
  }
}
