import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import {
  NgbDate,
  NgbDatepickerI18n,
  NgbDateStruct,
} from '@ng-bootstrap/ng-bootstrap';
import { LangChangeEvent, TranslateService } from '@ngx-translate/core';
import * as moment from 'moment';

@Component({
  selector: 'app-booking-date-picker',
  providers: [
    { provide: NgbDatepickerI18n, useClass: BookingDatePickerComponent },
  ],
  templateUrl: './booking-date-picker.component.html',
  styleUrls: ['./booking-date-picker.component.scss'],
})
export class BookingDatePickerComponent
  extends NgbDatepickerI18n
  implements OnInit
{
  @Input() fromDate: NgbDate;
  @Input() toDate: NgbDate | null = null;
  @Output() dateSelect: EventEmitter<any> = new EventEmitter();
  @ViewChild('btnRerender') btnRerender: ElementRef;

  public hoveredDate: NgbDate | null = null;

  constructor(
    private translate: TranslateService
  ) {
    super();
    this.translate.onLangChange.subscribe((event: LangChangeEvent) => {
      // TODO This as a workaround.
      this.btnRerender?.nativeElement.click();
    });
  }

  async ngOnInit() {
    await this.translate.get('date-picker');
  }

  isDisabled(date) {
    const past = moment(`${date.year}/${date.month}/${date.day}`);
    const current = moment(`${new Date().getFullYear()}/${new Date().getMonth() + 1}/${new Date().getDate()}`).add(1, 'days');
    // console.log('date: ', past, current);
    return past.unix() < current.unix();
  }

  navigateTo(dp: any) {
    const ngbDatePkrList = document.querySelectorAll('select.custom-select');
    const month = ngbDatePkrList[0]['value'] * 1;
    const year = ngbDatePkrList[1]['value'] * 1;

    dp.navigateTo({year: year + 1, month, day: 1});
    dp.navigateTo({year, month, day: 1});
  }
 
  getWeekdayShortName(weekday: number): string {
    const datePickerLang = this.translate.instant('date-picker');
    const weekdays = [];
    if (datePickerLang?.weekdays) {
      Object.keys(datePickerLang?.weekdays)?.forEach((key) => {
        weekdays.push(datePickerLang.weekdays[key]);
      });
    }
    return weekdays[weekday - 1] || '';
  }
  getMonthShortName(month: number): string {
    const datePickerLang = this.translate.instant('date-picker');
    const months = [];
    if (datePickerLang?.months) {
      Object.keys(datePickerLang?.months)?.forEach((key) => {
        months.push(datePickerLang.months[key]);
      });
    }
    return months[month - 1] || '';
  }
  getMonthFullName(month: number): string {
    return this.getMonthShortName(month);
  }
  getDayAriaLabel(date: NgbDateStruct): string {
    return `${date.day}-${date.month}-${date.year}`;
  }

  onDateSelection(date) {
    if (this.isDisabled(date)) {
      return;
    }
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date.after(this.fromDate)) {
      this.toDate = date;
    } else {
      this.toDate = null;
      this.fromDate = date;
    }
    this.dateSelect.emit({
      fromDate: this.fromDate,
      toDate: this.toDate,
    });
  }

  isHovered(date: NgbDate) {
    return (
      this.fromDate &&
      !this.toDate &&
      this.hoveredDate &&
      date.after(this.fromDate) &&
      date.before(this.hoveredDate)
    );
  }

  isInside(date: NgbDate) {
    return this.toDate && date.after(this.fromDate) && date.before(this.toDate);
  }

  isRange(date: NgbDate) {
    return (
      date.equals(this.fromDate) ||
      (this.toDate && date.equals(this.toDate)) ||
      this.isInside(date) ||
      this.isHovered(date)
    );
  }
}
