import {
  Component,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef,
  AfterViewInit,
  OnDestroy,
  OnInit
} from '@angular/core';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { HubModel } from 'src/app/shared/models/hub.models';
import { PAGES } from 'src/app/shared/constant/pages';
import { ACTIONS } from 'src/app/shared/constant/event';
import { faCamera, faExchangeAlt, faPaperPlane } from '@fortawesome/free-solid-svg-icons';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-scan-face',
  templateUrl: './scan-face.component.html',
  styleUrls: ['./scan-face.component.scss']
})
export class ScanFaceComponent implements AfterViewInit, OnDestroy, OnInit {

  public faExchange = faExchangeAlt;
  public faCamera = faCamera;
  public faPaperPlane = faPaperPlane;

  @Output() nextStep: EventEmitter<any> = new EventEmitter();
  @Output() backStep: EventEmitter<any> = new EventEmitter();
  @Output() image: EventEmitter<any> = new EventEmitter();
  

  @ViewChild('video')
  public video: ElementRef;

  public imageBase64 = '';
  private hubSubcription;

  constructor(private hubService: HubService, private translateService: TranslateService) {
    this.hubSubcription = this.hubService.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.scanFace) {
        return;
      }
      if (res.message === ACTIONS.nextStep) {
        this.changeToNextStep();
      } else if (res.message === ACTIONS.backStep) {
        this.changeToPreviousStep();
      }
      if (res.message === ACTIONS.closeCamera) {
        this.turnOffVideo();
      }
    });
  }

  ngOnInit() {}

  ngOnDestroy(): void {
    this.hubSubcription.unsubscribe();
  }

  async ngAfterViewInit() {
    await this.setupDevice();
  }
  /**
   * Setup video stream
   */
  async setupDevice() {
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: true
        });
        if (stream) {
          this.video.nativeElement.srcObject = stream;
          this.video.nativeElement.play();
        } else {
          this.hubService.error(this.translateService.instant('msg.noOutputVideoDevice'));
          // console.log('You have no output video device');
        }
      } catch (e) {
        console.error(e);
      }
    }
  }
  /**
   * Take photo
   */
  takePhoto() {
    const nativeVideo = this.video.nativeElement;
    nativeVideo.pause();

    const canvas = document.createElement('canvas');
    canvas.width = nativeVideo.videoWidth;
    canvas.height = nativeVideo.videoHeight;
    canvas
      .getContext('2d')
      .drawImage(nativeVideo, 0, 0, canvas.width, canvas.height);
    this.imageBase64 = canvas.toDataURL();
  }
  /**
   * Resume
   */
  resume() {
    const nativeVideo = this.video.nativeElement;
    nativeVideo.play();
    this.imageBase64 = '';
  }
  submit() {
    if (this.imageBase64) {
      this.image.emit(this.imageBase64); 
    } else {
      this.hubService.error(this.translateService.instant('msg.noFaceImageToDetect'));
    }
  }
  /**
   * turnOffVideo
   */
  turnOffVideo() {
    const nativeVideo = this.video.nativeElement;
    nativeVideo.srcObject = null;
  }
  /**
   * Navigate
   */
  changeToNextStep() {
    if (this.imageBase64) {
      this.nextStep.emit();
    } else {
      this.takePhoto();
    }
    this.video.nativeElement.play();
  }

  changeToPreviousStep() {
    this.backStep.emit();
  }
}
