import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router, RoutesRecognized } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { HubModel } from 'src/app/shared/models/hub.models';
import { ACTIONS } from 'src/app/shared/constant/event';
import { faArrowLeft, faLanguage } from '@fortawesome/free-solid-svg-icons';
import { Location } from '@angular/common';
import { PAGES, STEPS } from 'src/app/shared/constant/pages';
import { filter, pairwise } from 'rxjs/operators';
import { UtilityService } from 'src/app/shared/services/utility/utility.service';

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss'],
})
export class NavigationComponent implements OnInit, OnDestroy {
  public faArrowLeft = faArrowLeft;
  public faLanguage = faLanguage;

  public siteLang = {
    key: 'vi',
    image: '',
    label: 'lang.vi',
  };

  public langs = [
    {
      key: 'vi',
      image: './assets/images/vi-32.png',
      label: 'lang.vi',
    },
    {
      key: 'en',
      image: './assets/images/en-32.png',
      label: 'lang.en',
    },
  ];

  private subcribe = [];

  constructor(
    private router: Router,
    private translate: TranslateService,
    private modalService: NgbModal,
    private hubService: HubService,
    private location: Location,
    private utilService: UtilityService
  ) {
    const sub = this.router.events
      .pipe(
        filter((evt: any) => evt instanceof RoutesRecognized),
        pairwise()
      )
      .subscribe((events: RoutesRecognized[]) => {
        // console.log('previous url', events[0].urlAfterRedirects);
        // console.log('current url', events[1].urlAfterRedirects);
        const previousUrl = events[0].urlAfterRedirects;
        const currentUrl = events[1].urlAfterRedirects;
        const previousStep = this.utilService.getCurrentStep(previousUrl);
        const currentStep = this.utilService.getCurrentStep(currentUrl);
        const currentPage = this.utilService.getCurrentPage();

        let exactCurrentPage =
          currentPage === PAGES.walkin ? STEPS.step2 : STEPS.step3;
        console.log(
          'ROUTE CHANGE: ',
          previousStep,
          currentStep,
          exactCurrentPage
        );
        if (previousStep !== STEPS.detect && currentStep === exactCurrentPage) {
          this.router.navigate([`/${currentPage}/${STEPS.detect}`]);
        }
      });
    this.subcribe = [sub];
  }
  ngOnDestroy(): void {
    this.subcribe.forEach((s) => s.unsubscribe());
  }

  ngOnInit(): void {
    const defaultLangKey = 'vi';
    const defaultLang = this.langs.find((l) => l.key === defaultLangKey);
    this.changeLanguage(defaultLang.key, defaultLang.image, defaultLang.label);
  }

  goBack(): void {
    const splited = this.router.url.split(/[\/?]/);
    const step = splited.pop();
    const page = splited.pop();
    if (step === 'detect') {
      const event: HubModel = {
        receiver: PAGES.flowDetect,
        message: ACTIONS.backStep,
      };
      this.hubService.sendEvent(event);
    } else if (step === 'step2') {
      this.router.navigateByUrl(`/${page}/${STEPS.step1}`);
    } else if (step === 'step3') {
      this.router.navigateByUrl(`/${page}/${STEPS.step2}`);
    } else if (step === 'step4' || step === 'step1') {
      this.router.navigateByUrl('/options');
    } else if (step === 'options') {
      this.router.navigateByUrl('/');
    } else {
      this.location.back();
    }
  }

  changeLanguage(lang: string, imgSrc: string, label: string): void {
    this.translate.use(lang);
    this.siteLang.image = imgSrc;
    this.siteLang.key = lang;
    this.siteLang.label = label;
    this.modalService.dismissAll();
  }

  openChangeLang(content: any) {
    this.modalService.open(content, { centered: true });
  }
}
