import { Injectable } from '@angular/core';
import {
  HttpEvent,
  HttpInterceptor,
  HttpHandler,
  HttpRequest,
  HttpResponse,
  HttpHeaders
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { map, catchError, switchMap } from 'rxjs/operators';
import { AuthService } from 'src/app/core/auth.service';
import { LOCAL_STORAGE_KEYS } from 'src/app/shared/constant/local-storage';
import { environment } from 'src/environments/environment';
import { ConfigureService } from '../configure/configure.service';

@Injectable()
export class HttpInterceptorService implements HttpInterceptor {
  private BaseUrl = '';

  constructor(private authService: AuthService) {}

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    if (req.url.includes('api') && !req.url.includes('http')) {
      this.BaseUrl = this.getBaseUrl();

      const token = window.localStorage.getItem(LOCAL_STORAGE_KEYS.token) || '';
      const headers = new HttpHeaders({
        Authorization: token,
        'Content-Type': 'application/json'
      });

      const modified = req.clone({
        headers,
        url: `${this.BaseUrl}/${req.url}`
      });

      return next.handle(modified).pipe(
        map((event: HttpEvent<any>) => {
          if (event instanceof HttpResponse) {
            event = event.clone({ body: this.modifyBody(event.body) });
          }
          return event;
        }),
        catchError(err => {
          return this.handleError(err, req, next);
        })
      );
    }

    return next.handle(req);
  }

  private modifyBody(body: any) { }

  private handleError(error: any, req: HttpRequest<any>, next: HttpHandler) {
    if (error.status === 401) {
      return this.authService.getToken().pipe(
        switchMap((res: any) => {
          const token = `${res.token_type} ${res.access_token}`;
          const headers = new HttpHeaders({
            Authorization: token,
            'Content-Type': 'application/json'
          });

          window.localStorage.setItem(LOCAL_STORAGE_KEYS.token, token);
          this.BaseUrl = this.getBaseUrl();

          const modified = req.clone({
            headers,
            url: `${this.BaseUrl}/${req.url}`
          });
          return next.handle(modified);
        })
      );
    }
    return throwError(error);
  }

  private getBaseUrl():string {
    return ConfigureService.config.url.base || '';
  }
}
