import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  OnDestroy,
  ElementRef,
} from '@angular/core';
import { Router } from '@angular/router';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { HubModel } from 'src/app/shared/models/hub.models';
import { PAGES, STEPS } from 'src/app/shared/constant/pages';
import { ACTIONS } from 'src/app/shared/constant/event';
import { BookingService } from '../../services/booking/booking.service';
import { SearchBookingModel } from '../../models/booking/search-booking.model';
import { ResponseBody } from 'src/app/shared/models/response';
import { TranslateService } from '@ngx-translate/core';
import { HubWalkinService } from 'src/app/shared/services/hub-walkin/hub-walkin.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-scan-qr',
  templateUrl: './scan-qr.component.html',
  styleUrls: ['./scan-qr.component.scss'],
})
export class ScanQrComponent implements OnInit, OnDestroy {
  @Output() redirectBookingCode: EventEmitter<any> = new EventEmitter();
  @Output() nextStep: EventEmitter<any> = new EventEmitter();

  private hubSubcription = null;
  private readString = '';
  private wheelHandler: any;

  constructor(
    private hub: HubService,
    private hubWalkin: HubWalkinService,
    private router: Router,
    private bookingService: BookingService,
    private translateService: TranslateService,
  ) {
    this.hubSubcription = this.hub.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.scanQR) {
        return;
      }
      if (res.message === ACTIONS.nextStep) {
        this.changeToNextStep();
      } else if (res.message === ACTIONS.backStep) {
        this.backStep();
      } else if (res.message === ACTIONS.focusQrInput) {
        this.addListener();
      }
    });
  }

  ngOnDestroy(): void {
    this.hubSubcription.unsubscribe();
    this.clearListener();
  }

  ngOnInit(): void {
    this.hubWalkin.setStep(1);
    this.addListener();
  }

  addListener() {
    this.wheelHandler = this.listenQrCode.bind(this);
    const node = window.document;
    node.addEventListener('keypress', this.wheelHandler, true);
  }

  clearListener() {
    const node = window.document;
    console.log('clwar');
    node.removeEventListener('keypress', this.wheelHandler, true);
  }

  listenQrCode(event) {
    if (event.keyCode === 13) {
      const value = this.readString;
      this.readString = '';
      console.log('qr decode: ', value);
      if (value !== '' && !this.hub.getLoading()) {
        console.log('start get booking');
        this.getBooking(value);
      }
    } else {
      this.readString += event?.key || '';
    }
  }

  getBooking(rsvCode: string): void {
    this.hub.setLoading(true);
    const model: SearchBookingModel = {
      ReservationNo: rsvCode,
    };
    this.bookingService
      .searchBooking(model)
      .subscribe(
        (res: ResponseBody) => {
          if (res.statusCode === 200 && res.metadata) {
            const data = res.metadata[0];
            if (!environment.production) {
              this.changeToNextStep({ ...(data || {}), resvNo: rsvCode });
              return;
            }
            if (data) {
              this.changeToNextStep({ ...data, resvNo: rsvCode });
              this.clearListener();
            } else {
              let msg = `${this.translateService.instant(
                'msg.notFoundBooking'
              )} <p class="work-break-all">${rsvCode}</p>`;
              this.hub.error(msg);
            }
            console.log(res.metadata);
          } else {
            let msg = `${this.translateService.instant(
              'msg.errorSearchBooking'
            )} <p class="work-break-all">${rsvCode}</p>`;
            this.hub.error(msg);
            console.log(res.message);
          }
        },
        () => {
          this.hub.error(
            this.translateService.instant(`msg.errorSearchBooking`)
          );
        }
      )
      .add(() => {
        this.hub.setLoading(false);
      });
  }
  /**
   * Back Step
   */
  backStep() {
    // this.router.navigateByUrl('/options');
    this.clearListener();
    this.readString = '';
  }

  changeToNextStep(data?: any): void {
    // this.nextStep.emit(data);
    this.router.navigate([`/${PAGES.checkinQr}/${STEPS.step2}`]);
    this.hubWalkin.setBookingData(data);
    this.readString = '';
  }
}
