import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { HubModel } from '../../models/hub.models';

@Injectable({
  providedIn: 'root'
})
export class HubWalkinService {

  private step: Subject<any> = new Subject();
  // private step = 1;
  public page = '';
  public bookingData:any = {};
  public totalRateData: any = {};
  public isDoneFlow = true;
  public errorMessageCreateBooking = '';
  public isShowStepper = false;

  public textIntro = '';
  public subTextIntro = '';

  constructor() { }

  getBookingData() {
    return this.bookingData;
  }

  setBookingData(data) {
    this.bookingData = {...this.bookingData, ...data};
  }

  clearBookingData() {
    this.bookingData = {};
  }

  setStep(event: any): void {
    this.step.next(event);
  }

  onStep(): Observable<any> {
    return this.step.asObservable();
  }

  setPage(page: string) {
    this.page = page;
  }

  setIsShowStepper(value) {
    this.isShowStepper = value;
  }

  setTextIntro(value) {
    this.textIntro = value;
  }

  setSubTextIntro(value) {
    this.subTextIntro = value;
  }

}
