import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import Stepper from 'bs-stepper';
import { ACTIONS } from 'src/app/shared/constant/event';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { HubModel } from 'src/app/shared/models/hub.models';
import { mergeMap } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { DispenserService } from 'src/app/shared/services/dispenser/dispenser.service';
import * as moment from 'moment';
import { LockCardModel } from 'src/app/shared/models/lock-card.model';
import { LockCardService } from 'src/app/shared/services/lock-card/lock-card.service';
import { PAGES } from 'src/app/shared/constant/pages';
import { CHECK_IN_MODES } from 'src/app/shared/constant/check-mode';
import { ActivatedRoute, Router } from '@angular/router';
import { UtilityService } from 'src/app/shared/services/utility/utility.service';
import { AUDIO_FILES } from 'src/app/shared/constant/audio-files';
import { environment } from 'src/environments/environment';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { FlowDetectComponent } from '../flow-detect/flow-detect.component';
import { BookingService } from '../../services/booking/booking.service';
import { ResponseBody } from 'src/app/shared/models/response';
import { TranslateService } from '@ngx-translate/core';
import { RoomAvailabilityService } from '../../services/room-availability/room-availability.service';
import { faTruckMonster } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-flow-checkin',
  templateUrl: './flow-checkin.component.html',
  styleUrls: ['./flow-checkin.component.scss'],
  animations: [
    trigger('btnAppear', [
      state(
        'visible',
        style({
          display: 'flex',
          opacity: 1,
          width: '100%',
        })
      ),
      state(
        'hidden',
        style({
          display: 'none',
          opacity: 0,
          width: 0,
        })
      ),
      transition('hidden => visible', [animate('.1s')]),
      transition('visible => hidden', [animate('.1s')]),
    ]),
  ],
})
export class FlowCheckinComponent implements OnInit, OnDestroy {
  public mode = CHECK_IN_MODES.BOOKING_CODE;

  public checkInModes = { ...CHECK_IN_MODES };
  public bookingData: any = {};
  public totalRateData: any = {};

  private stepper: Stepper;

  public showFlowDetect: boolean = false;
  public showFooter: boolean = true;
  public isDoneFlow = true;

  private hubSubcription = null;

  public error = {
    fetchRoomAvai: '',
  };
  public roomAvaiData = [];

  @ViewChild('flowDetectComp') flowDetectComp: FlowDetectComponent;

  constructor(
    private hubService: HubService,
    private dispenserService: DispenserService,
    private lockCardService: LockCardService,
    private route: ActivatedRoute,
    private utilService: UtilityService,
    private router: Router,
    private bookingService: BookingService,
    private roomAvaiService: RoomAvailabilityService,
    private translateService: TranslateService
  ) {
    this.subcribeHub();
  }

  ngOnDestroy(): void {
    this.hubSubcription.unsubscribe();
  }

  next() {
    this.stepper.next();
  }

  subcribeHub(): void {
    this.hubSubcription = this.hubService.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.flowCheckin) {
        return;
      }
      console.log('FLOW-CHECKIN: ', res);
      if (res.message === ACTIONS.goBack) {
        this.back();
      }
    });
  }

  back() {
    const current = this.getActiveStep();
    const backToOptionsScreen = [
      PAGES.scanQR,
      PAGES.bookingCode,
      PAGES.bookingDone,
    ];
    if (this.showFlowDetect) {
      this.changeStepInsideFlowDetect(this.flowDetectComp.progress.key);
      return;
    }
    if (current === PAGES.scanQR) {
      const event: HubModel = {
        receiver: PAGES.scanQR,
        message: ACTIONS.backStep,
      };

      this.hubService.sendEvent(event);
    }
    if (backToOptionsScreen.includes(current)) {
      this.router.navigateByUrl('/options');
      return;
    }
    this.stepper.previous();
  }

  ngOnInit() {
    this.route.queryParams.subscribe((params) => {
      this.mode = params.mode;

      setTimeout(() => {
        this.initStepper();
        this.addListener();
      }, 0);
    });
  }

  initStepper() {
    const linear = environment.production;

    this.stepper = new Stepper(document.querySelector('#stepper-checkin'), {
      linear: linear,
      animation: true,
    });
  }

  receiveGuestData(guestData: any) {
    setTimeout(() => {
      this.hubService.setLoading(true);
    }, 0);
    this.bookingData = { ...this.bookingData, ...guestData };
    this.bookingService
      .getTotalRate({
        ArrivalDate: this.bookingData.arrivalDate,
        DepartDate: this.bookingData.departDate,
        ArrivalTime: this.bookingData.arrivalTime,
        DepartTime: this.bookingData.departTime,
        RoomBocked: this.bookingData.roomCode,
        RoomType: this.bookingData.roomTypeCode,
        NoOfAdults: this.bookingData.adult,
        NoOfChild: this.bookingData.child,
      })
      .pipe(
        mergeMap((res: ResponseBody) => {
          if (res.metadata && res.metadata[0]) {
            this.totalRateData = res.metadata[0];
            return this.roomAvaiService.getRoomAvailabitlity(
              this.bookingData.arrivalDate,
              this.bookingData.departDate,
              true
            );
          } else {
            throwError({
              code: '',
              message: this.translateService.instant('msg.errorGetTotalRate'),
              useToat: faTruckMonster,
            });
          }
        })
      )
      .subscribe(
        (res: ResponseBody) => {
          if (res.statusCode === 200 && res.metadata) {
            this.roomAvaiData = res.metadata
              .map((item) => ({
                ...item,
                buildingCode: `${item.buildingCode}`,
              }))
              .filter(
                (item) => item?.roomTypeCode === this.bookingData.roomTypeCode
              );
            if (this.roomAvaiData[0]) {
              this.changeRoomCode(this.roomAvaiData[0].roomCode);
            }
            this.next();
          } else {
            this.error.fetchRoomAvai = res.message;
            throwError({
              code: '',
              message: this.translateService.instant('msg.errorGetTotalRate'),
              useToat: false,
            });
          }
        },
        (err: any) => {
          if (err.useToast) {
            this.hubService.error(
              err?.message ||
                this.translateService.instant('msg.errorHandlingRequest')
            );
          }
        }
      ).add(() => {
        this.hubService.setLoading(false);
      });
  }

  refreshRoomAvai() {
    console.log('refreshRoomAvai');
    this.fetchRoomAvai(
      this.bookingData.arrivalDate,
      this.bookingData.departDate
    );
  }

  fetchRoomAvai(arr, dpt) {
    this.error.fetchRoomAvai = '';

    this.hubService.setLoading(true);
    this.roomAvaiService
      .getRoomAvailabitlity(arr, dpt, true)
      .subscribe(this.fetchRoomAvailObserver())
      .add(() => {
        this.hubService.setLoading(false);
      });
  }

  fetchRoomAvailObserver() {
    return {
      next: (res: ResponseBody) => {
        if (res.statusCode === 200 && res.metadata) {
          this.roomAvaiData = res.metadata
            .map((item) => ({
              ...item,
              buildingCode: `${item.buildingCode}`,
            }))
            .filter(
              (item) => item?.roomTypeCode === this.bookingData.roomTypeCode
            );
        } else {
          this.error.fetchRoomAvai = res.message;
        }
      },
      error: () => {
        this.error.fetchRoomAvai = this.translateService.instant(
          'msg.errorHandlingRequest'
        );
      },
    };
  }

  changeRoomCode(roomCode: string) {
    this.bookingData.roomCode = roomCode;
  }

  doneBookingDetail(data) {
    this.bookingData = { ...data };
    this.next();
  }

  addListener() {
    const stepperEl = document.querySelector('#stepper-checkin');

    stepperEl.addEventListener('shown.bs-stepper', (event: any) => {
      console.log({ event });
      const activeStep = this.getActiveStep();

      this.actionController(activeStep, event.detail);
    });
  }

  actionController(activeStep: string, detail: any) {
    if (activeStep === PAGES.scanQR) {
      this.showFooter = false;
      this.focusQrInput();
      this.utilService.playAudio(AUDIO_FILES.scanQrIntro);
    } else if (activeStep === PAGES.bookingCode) {
      this.showFooter = true;
      this.utilService.playAudio(AUDIO_FILES.bookingCodeIntro);
    } else if (activeStep === PAGES.bookingDetails) {
      this.showFooter = true;
    } else if (detail && detail.to === 2) {
      this.showFooter = false;
      if (detail.from === 1 || detail.from === 3) {
        this.openFlowDetect();
      }
    } else if (activeStep === PAGES.bookingDone) {
      this.showFooter = false;
      const audioFile = this.isDoneFlow ? AUDIO_FILES.bookingDoneIntro : AUDIO_FILES.bookingDoneError;
      this.utilService.playAudio(audioFile);
    }
  }

  /**
   *
   * X l khi nhn din xong
   */
  onVerify({ isDone = true }) {
    this.isDoneFlow = isDone;
    this.next();
  }
  /**
   * Reset Flow
   */
  resetFlow() {
    this.stepper.to(0);
  }

  /**
   * Focus  input  ly m
   */
  focusQrInput() {
    const event: HubModel = {
      receiver: PAGES.scanQR,
      message: ACTIONS.focusQrInput,
    };
    this.hubService.sendEvent(event);
  }

  /**
   * Bt popup chn giy t khi vo mn hnh Scan
   */
  openFlowDetect() {
    console.log('open flow detect');
    this.showFlowDetect = true;

    setTimeout(() => {
      const event: HubModel = {
        receiver: PAGES.flowDetect,
        message: ACTIONS.openScanModal,
      };

      this.hubService.sendEvent(event);
    }, 0);
  }
  /**
   *
   * Close Flow Detect
   */
  closeFlowDetect(guestData: any) {
    console.log('close flow detect: ', guestData, this.bookingData);
    if (guestData) {
      this.bookingData = { ...this.bookingData, ...guestData };
    }
    this.showFlowDetect = false;

    setTimeout(() => {
      this.initStepper();
      this.addListener();
      this.stepper.to(3);
    }, 0);
  }
  /**
   *
   * UPDATE STEP TRONG FLOW DETECT
   */
  changeStepInsideFlowDetect(detectStep: string) {
    if (detectStep === 'type') {
      this.showFlowDetect = false;
      this.closeFlowDetect(null);
    } else {
      this.openFlowDetect();
    }
    // switch (detectStep) {
    //   case 'type':
    //     this.showFlowDetect = false;
    //     this.closeFlowDetect(null);
    //     break;
    //   case 'front':
    //     this.flowDetectComp.progress.key = 'type';
    //     break;
    //   case 'back':
    //     this.flowDetectComp.progress.key = 'front';
    //     break;
    //   case 'face':
    //     this.flowDetectComp.progress.key = 'back';
    //     break;
    //   default:
    //     break;
    // }
  }
  /**
   * Nh th khi nhn din thnh cng
   */
  processCard(guestData: any) {
    this.bookingData = { ...this.bookingData, ...guestData };
    console.log('process card | booking data: ', this.bookingData);

    this.hubService.setLoading(true);
    this.bookingService
      .checkin({
        resvNo: this.bookingData?.resvNo,
        roomCode: this.bookingData?.roomCode,
      })
      .pipe(
        mergeMap((res: any) => {
          if (res.statusCode === 200) {
            return this.dispenserService.moveToRfPos();
          } else {
            const message = this.translateService.instant('msg.errorCheckin');
            return throwError({ code: res.statusCode, message });
          }
        }),
        mergeMap((res: any) => {
          if (res.code === 0) {
            return this.writeCard();
          } else {
            return throwError({ code: res.code, message: res.message });
          }
        }),
        mergeMap((res: any) => {
          if (res.code === 0) {
            return this.dispenserService.eject();
          } else {
            return throwError({ code: res.code, message: res.message });
          }
        })
      )
      .subscribe(
        (res: any) => {
          if (res.code === 0) {
            this.onVerify({ isDone: true });
          } else {
            this.hubService.error(res.message);
            this.onVerify({ isDone: false });
          }
        },
        ({ code, message }) => {
          if (!environment.production) {
            this.next();
            return;
          }
          this.hubService.error(message);
          this.onVerify({ isDone: false });
        }
      )
      .add(() => {
        this.hubService.setLoading(false);
      });
  }
  /**
   *
   * Ghi th
   */
  writeCard() {
    const lockArrDate = moment(this.bookingData['arrivalDate']).format(
      'yyyy-MM-DD'
    );
    const lockDeptDate = moment(this.bookingData['departDate']).format(
      'yyyy-MM-DD'
    );

    const lockArrTime = moment(this.bookingData['arrivalTime'], 'HH:mm').format(
      'HH:mm:ss'
    );
    const lockDeptTime = moment(this.bookingData['departTime'], 'HH:mm').format(
      'HH:mm:ss'
    );

    const body: LockCardModel = {
      room: this.bookingData['roomCode'],
      building: this.bookingData['buildingCode'],
      arrival: `${lockArrDate} ${lockArrTime}`,
      departure: `${lockDeptDate} ${lockDeptTime}`,
    };

    return this.lockCardService.writeCard(body);
  }
  /**
   * NAVIGATION
   */
  goToPreviousStep() {
    const componentName = this.getActiveStep();
    const type = ACTIONS.backStep;
    const data: HubModel = {
      receiver: componentName,
      message: type,
    };
    this.hubService.sendEvent(data);
  }

  gotoNextStep() {
    console.log('go to next step');
    const componentName = this.getActiveStep();
    const type = ACTIONS.nextStep;
    const data: HubModel = {
      receiver: componentName,
      message: type,
    };
    this.hubService.sendEvent(data);
  }

  getActiveStep() {
    const bsStepperContent = document.getElementById('bsStepperContent');
    const activeStep = bsStepperContent?.querySelector(
      '.dstepper-block.active'
    );
    return activeStep?.getAttribute('data-name') || '';
  }

  getActiveStepHeader() {
    const bsStepperHeader = document.querySelector('.bsStepperContent');
    const activeStep = bsStepperHeader?.querySelector('.step.active');
    return activeStep || '';
  }
}
