import {
  Component,
  OnInit,
  OnDestroy,
  Input,
  Output,
  EventEmitter,
} from '@angular/core';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { HubModel } from 'src/app/shared/models/hub.models';
import { PAGES, STEPS } from 'src/app/shared/constant/pages';
import { Router } from '@angular/router';
import { faCheckCircle } from '@fortawesome/free-regular-svg-icons';
import { faExclamation } from '@fortawesome/free-solid-svg-icons';
import { HubWalkinService } from 'src/app/shared/services/hub-walkin/hub-walkin.service';

@Component({
  selector: 'app-booking-done',
  templateUrl: './booking-done.component.html',
  styleUrls: ['./booking-done.component.scss'],
})
export class BookingDoneComponent implements OnDestroy, OnInit {
  // @Input() isDone = true;
  // @Input() errorMessage = '';
  @Output() reset: EventEmitter<any> = new EventEmitter();

  private hubSubcription;
  public faCheckCircle = faCheckCircle;
  public faExclamation = faExclamation;

  public isDone = true;
  public errorMessage = '';

  constructor(private hubService: HubService, private router: Router, public hubWalkin: HubWalkinService) {
    this.hubSubcription = this.hubService.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.bookingDone) {
        return;
      }
      this.router.navigateByUrl('/options');
    });
  }
  ngOnInit(): void {
    this.isDone = this.hubWalkin.isDoneFlow;
    this.errorMessage = this.hubWalkin.errorMessageCreateBooking;
    this.hubWalkin.setStep(4);
  }

  finish() {
    this.router.navigateByUrl('/options');
  }

  resetStepper() {
    // this.reset.emit();
    this.router.navigate([`/${PAGES.walkin}/${STEPS.step1}`]);
  }

  ngOnDestroy(): void {
    this.hubSubcription.unsubscribe();
  }
}
