import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LockCardModel } from '../../models/lock-card.model';
import { ConfigureService } from 'src/app/config/configure/configure.service';

@Injectable({
  providedIn: 'root'
})
export class LockCardService {

  private serviceUrl = '';

  constructor(private http: HttpClient) { 
    this.serviceUrl = ConfigureService.config.url.service;
  }

  /**
   * WriteCard
   */
  public writeCard(body: LockCardModel) {
    const endPoint = `${this.serviceUrl}/api/lock/write-card`;
    return this.http.post(endPoint, body);
  }

}
