import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CheckinModule } from './modules/checkin/checkin.module';
import { NavigationBarModule } from './modules/navigation-bar/navigation-bar.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { HttpClientModule, HttpClient, HTTP_INTERCEPTORS } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { HttpInterceptorService } from './config/httpInterceptor/http-interceptor.service';
import { APP_INITIALIZER } from '@angular/core';
import { ConfigureService } from './config/configure/configure.service';
import { WalkinComponent } from './layout/walkin/walkin.component';
import { CheckinBoookingCodeComponent } from './layout/checkin-boooking-code/checkin-boooking-code.component';
import { CheckinQrComponent } from './layout/checkin-qr/checkin-qr.component';


export const configFactory = (configService: ConfigureService) => {
  return (): Promise<any> => { 
    return configService.loadConfig();
  }
};

@NgModule({
  declarations: [AppComponent, WalkinComponent, CheckinBoookingCodeComponent, CheckinQrComponent],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    FlexLayoutModule,
    NavigationBarModule,
    CheckinModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    NgbModule,
    FontAwesomeModule,
    BrowserAnimationsModule,
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: HttpInterceptorService,
      multi: true,
    },
    {
      provide: APP_INITIALIZER,
      useFactory: configFactory,
      deps: [ConfigureService],
      multi: true,
    },
    // {
    //   provide: LocationStrategy,
    //   useClass: HashLocationStrategy,
    // }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}

export function HttpLoaderFactory(http: HttpClient): TranslateHttpLoader {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}


