import {
  Component,
  EventEmitter,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { Router } from '@angular/router';
import { faExchangeAlt } from '@fortawesome/free-solid-svg-icons';
import { TranslateService } from '@ngx-translate/core';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import { ConfigureService } from 'src/app/config/configure/configure.service';
import { AUDIO_FILES } from 'src/app/shared/constant/audio-files';
import { ACTIONS } from 'src/app/shared/constant/event';
import { PAGES, STEPS } from 'src/app/shared/constant/pages';
import { HubModel } from 'src/app/shared/models/hub.models';
import { HubWalkinService } from 'src/app/shared/services/hub-walkin/hub-walkin.service';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { OcrService } from 'src/app/shared/services/ocr/ocr.service';
import { PassportReaderService } from 'src/app/shared/services/passportReader/passport-reader.service';
import { UtilityService } from 'src/app/shared/services/utility/utility.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-flow-detect',
  templateUrl: './flow-detect.component.html',
  styleUrls: ['./flow-detect.component.scss'],
})
export class FlowDetectComponent implements OnInit, OnDestroy {
  @Output() sendGuestInfo: EventEmitter<any> = new EventEmitter();
  @Output() backStep: EventEmitter<any> = new EventEmitter();

  public faExchange = faExchangeAlt;

  public PASSPORT = 'passport';
  public IDCARD = 'idCard';
  public LICENSE = 'license';
  public IDCARD_NEW = 'idCard_new';
  public INDEX_FRONT = 1;
  public INDEX_BACK = 2;
  public INDEX_FACE = 3;

  public progressBar = [
    {
      index: 0,
      key: 'type',
      value: 10,
      name: 'Chn loi giy t',
      notice:
        'Chn mt trong nhng loi giy t pha trn  my qut ly thng tin',
      audio: AUDIO_FILES.scanIdDocType,
      textIntro: 'flowDetect.hi',
      subTextIntro: 'flowDetect.docIntro',
    },
    {
      index: 1,
      key: 'front',
      value: 25,
      name: 't mt trc ca giy t xung my qut',
      notice: 'Sau khi t, ch 1-2 giy  thit b tin hnh qut',
      audio: AUDIO_FILES.scanIdFront,
      textIntro: 'flowDetect.hi',
      subTextIntro: 'flowDetect.frontIntro',
    },
    {
      index: 2,
      key: 'back',
      value: 50,
      name: 't mt sau ca giy t xung my qut',
      notice: 'Sau khi t, ch 1-2 giy  thit b tin hnh qut',
      audio: AUDIO_FILES.scanIdBack,
      textIntro: 'flowDetect.hi',
      subTextIntro: 'flowDetect.backIntro',
    },
    {
      index: 3,
      key: 'face',
      value: 75,
      name: 'Xc minh khun mt',
      notice:
        'Vui lng nhn thng vo camera ca thit b ri nhn nt "Chp nh". Khi  hi lng vi nh, hy nhn nt "Xc minh" v ch mt cht  thit b nhn din.',
      audio: AUDIO_FILES.scanIdFace,
      textIntro: 'flowDetect.hi',
      subTextIntro: 'flowDetect.faceIntro',
    },
  ];

  public progress = {
    index: 0,
    key: 'type',
    value: 10,
    name: 'Chn loi giy t',
    notice:
      'Chn mt trong nhng loi giy t pha trn  my qut ly thng tin',
    audio: AUDIO_FILES.scanIdDocType,
    textIntro: '',
    subTextIntro: '',
  };

  public instruction = {
    idCard: {
      img: 'https://cdn2.hubspot.net/hubfs/3308097/Aila-ID-scan-TruScan.gif',
      buttonName: 'scanIdCard.scanPassport',
      sentence: [
        'Xin mi t mt trc ca chng minh th vo my qut',
        'Xin mi t mt sau ca chng minh th vo my qut',
      ],
    },
    passport: {
      img: 'https://www.hitouch.com/upload/images/201804/17/201804176365957939499905063885355.gif',
      buttonName: 'scanIdCard.scanIdCard',
      sentence: [
        'Xin mi t mt trc h chiu vo my qut',
        'Xin mi t mt trc ca h chiu vo my qut',
      ],
    },
  };

  public guest = {
    guestName: '',
    address: '',
    birth: '',
    expiry: '',
    passportNumber: '',
    passportType: '',
    sex: '',
    nationalityCode: '',
  };

  public alert = {
    errorModal: {
      show: false,
      type: '',
      message: '',
    },
    successModal: {
      show: false,
      type: '',
      message: '',
    },
    successOutside: {
      show: false,
      type: '',
      message: '',
    },
  };

  public noImage = `./assets/images/no-image.svg`;

  private cardImages = [];

  public seletedInstruction = {
    key: 'passport',
    img: '',
    buttonName: '',
    sentence: '',
  };

  private cardImageIndex = 0;
  private skipCallApiProcessImage = [this.PASSPORT];
  private isProcessFaceSuccess = false;
  private isProcessImageSuccess = false;
  private isProcessImageDone = false;

  private hubSubcription;

  constructor(
    private hubService: HubService,
    private utilService: UtilityService,
    private passportReader: PassportReaderService,
    private ocrService: OcrService,
    private translateService: TranslateService,
    private hubWalkin: HubWalkinService,
    private router: Router,
  ) {
    this.hubSubcription = this.hubService.onHub().subscribe((res: HubModel) => {
      console.log('detect: ', res);
      if (res.receiver !== PAGES.flowDetect) {
        return;
      }
      this.processHubEvent(res);
    });
  }

  ngOnInit(): void {
    setTimeout(() => {
      this.hubWalkin.setIsShowStepper(false);
    }, 100);
    this.hubWalkin.setTextIntro('flowDetect.hi');
    this.hubWalkin.setSubTextIntro('flowDetect.docIntro');
  }

  ngOnDestroy(): void {
    this.hubSubcription.unsubscribe();
    this.hubWalkin.setIsShowStepper(true);
    this.hubWalkin.setTextIntro('');
    this.hubWalkin.setSubTextIntro('');
    this.closeScanModal();
  }

  processHubEvent(event: HubModel) {
    if (event.message === ACTIONS.nextStep) {
      this.changeToNextStep();
    } else if (event.message === ACTIONS.backStep) {
      this.changeToPreviousStep();
    } else if (event.message === ACTIONS.cardContent) {
      this.processCardContent(event.data);
    } else if (event.message === ACTIONS.cardImage) {
      this.processCardImage(event.data);
    } else if (event.message === ACTIONS.openScanModal) {
      this.openScanModal();
    }
  }

  modalNext() {
    this.cardImageIndex += 1;
    this.progress = this.progressBar[this.cardImageIndex];
    this.utilService.playAudio(this.progress.audio);
    this.hubWalkin.setTextIntro(this.progress.textIntro);
    this.hubWalkin.setSubTextIntro(this.progress.subTextIntro);
  }

  modalBack() {
    this.cardImageIndex -= 1;
    this.progress = this.progressBar[this.cardImageIndex];
    this.utilService.playAudio(this.progress.audio);
  }

  chooseMethod(method: string) {
    this.seletedInstruction = { ...this.instruction[method], key: method };
    this.cardImageIndex = 0;
  }

  scanFront() {
    if (!environment.production) {
      this.isProcessImageDone = true;
      this.isProcessImageSuccess = true;
      this.modalNext();
      return;
    }
    if (
      !this.skipCallApiProcessImage.includes(this.seletedInstruction.key) &&
      this.cardImageIndex === this.INDEX_FRONT
    ) {
      this.isProcessImageSuccess = false;
      this.isProcessImageDone = false;
      const image = this.cardImages[this.INDEX_FRONT];

      if (!image) {
        this.hubService.error(this.translateService.instant('msg.notHaveFrontImage'));
        return;
      }

      this.callImageApi(this.seletedInstruction.key, image)
        .subscribe(
          (res: any) => {
            this.processImageResponse(this.seletedInstruction.key, res)();
            this.isProcessImageSuccess = true;
          },
          (err: any) => {
            const message = this.translateService.instant('msg.errorScanFront');
            this.alert.errorModal = {
              type: 'danger',
              message,
              show: false,
            };
            this.isProcessImageSuccess = false;
          }
        )
        .add(() => {
          this.isProcessImageDone = true;
        });
    }
    this.modalNext();
  }

  /**
   * NAVIGATE
   */
  changeToNextStep() {
    if (this.isProcessFaceSuccess) {
      this.sendGuestData();
      this.resetSelectedInstruction();
      this.resetHandleImage();
      this.closeCamera();
    } else {
      const btn = document.getElementById('btn-open-scan-modal');
      btn.click();
    }
  }

  changeToPreviousStep() {
    this.resetSelectedInstruction();
    this.resetHandleImage();
    this.closeCamera();
    if (this.progress.key === 'type') {

      this.backToScanId();
      // this.router.navigate([`/${PAGES.walkin}/${STEPS.step2}`]);
    } else {
      this.openScanModal();
    }
  }

  sendGuestData() {
    // this.sendGuestInfo.emit(this.guest);
    this.hubWalkin.setBookingData(this.guest);
    // this.router.navigate([`/${PAGES.walkin}/${STEPS.step2}`]);
    this.backToScanId();
  }

  backToScanId() {
    const page = this.utilService.getCurrentPage();
    console.log('back to scan id: ', page)
    switch (page) {
      case PAGES.walkin:
        this.router.navigate([`/${page}/${STEPS.step2}`]);
        break;
      case PAGES.checkinQr:
        this.router.navigate([`/${page}/${STEPS.step3}`]);
        break;
      default:
        break;
    }
  }

  /**
   * Scan Face
   */
  onScanFace(base64: string) {
    if (!environment.production) {
      this.progress.value = 100;
      this.alert.successOutside.message = this.translateService.instant('msg.successScanFace');
      this.showAlert('successOutside');
      this.closeScanModal();
      return;
    }

    this.hubService.setLoading(true);
    this.cardImages[this.cardImageIndex] = base64;

    const source = this.cardImages[this.INDEX_FRONT];
    const input = base64;

    const observer = {
      next: res => {
        const min = Number(ConfigureService.config.faceDistance.min);
        const max = Number(ConfigureService.config.faceDistance.max); 
        const distance = res['l2 distance'];
        // alert(`${JSON.stringify(res)} ${min} ${max}`);
        if (distance && min <= distance && distance <= max) {
          this.isProcessFaceSuccess = true;
          this.progress.value = 100;
          this.alert.successOutside.message = this.translateService.instant('msg.successScanFace');
          this.showAlert('successOutside');
          this.closeScanModal();
        } else {
          this.isProcessFaceSuccess = false;
          this.alert.errorModal.message = this.translateService.instant('msg.notDetected');
          this.alert.errorModal.type = 'danger';
          this.showAlert('errorModal');
          this.cardImageIndex = 0;
          this.progress = this.progressBar[this.cardImageIndex];
          this.cardImages = [];
        }
      },
      error: () => {
        this.isProcessFaceSuccess = false;
        this.alert.errorModal.message = this.translateService.instant('msg.errorScanFace');
        this.alert.errorModal.type = 'danger';
        this.showAlert('errorModal');
        this.cardImageIndex = 0;
        this.progress = this.progressBar[this.cardImageIndex];
        this.cardImages = [];
      },
    };

    const interval = window.setInterval(() => {
      if (this.isProcessImageDone) {
        if (!this.isProcessImageSuccess) {
          this.showAlert('errorModal');
          this.chooseMethod(this.seletedInstruction.key);
          this.cardImages = [];
          this.hubService.setLoading(false);
          window.clearInterval(interval);
          return;
        }

        this.ocrService
          .faceEmbedding(source, input)
          .subscribe(observer)
          .add(() => {
            this.hubService.setLoading(false);
          });
        window.clearInterval(interval);
      }
    }, 100);
  }
  /**
   *
   * Qut vi TH loi giy t l h chiu
   */
  processCardContent(data: any) {
    if (!this.skipCallApiProcessImage.includes(this.seletedInstruction.key)) {
      return;
    }
    if (
      this.skipCallApiProcessImage.includes(this.seletedInstruction.key) &&
      this.cardImageIndex !== this.INDEX_FRONT
    ) {
      return;
    }

    this.isProcessImageDone = true;

    this.guest = {
      birth: data['Date of birth'] || '',
      expiry: data['Date of expiry'] || '',
      guestName: data['National name'] || '',
      passportNumber: data['Passport number'] || '',
      passportType: data['Passport type'] || '',
      address: '',
      sex: data['Sex'] || '',
      nationalityCode: data['Nationality code'] || '',
    };

    if (!this.guest.passportNumber) {
      this.isProcessImageSuccess = false;
      const message = this.translateService.instant('msg.errorScanFront');
      this.alert.errorModal = {
        type: 'danger',
        message,
        show: false,
      };
      return;
    }

    if (moment(this.guest.birth).isValid()) {
      this.guest.birth = moment(this.guest.birth).format('DD/MM/yyyy');
    }
    if (moment(this.guest.expiry).isValid()) {
      this.guest.expiry = moment(this.guest.expiry).format('DD/MM/yyyy');
    }

    this.isProcessImageSuccess = true;
  }

  processCardImage(image: any) {
    this.cardImages[this.cardImageIndex] = image;
  }

  resetScannedImage(index: number) {
    this.cardImages[index] = '';
  }

  resetSelectedInstruction() {
    this.seletedInstruction = {
      buttonName: '',
      img: '',
      key: '',
      sentence: '',
    };
    this.guest = {
      address: '',
      guestName: '',
      birth: '',
      expiry: '',
      passportNumber: '',
      passportType: '',
      nationalityCode: '',
      sex: '',
    };
    this.cardImages = ['', ''];
    this.resetAlert();
  }

  resetHandleImage() {
    this.isProcessImageSuccess = false;
    this.isProcessImageDone = false;
    this.isProcessFaceSuccess = false;
  }

  /** PASSPORT SCANNER */
  startPassportReader() {
    this.passportReader.setConnectionValue(this.passportReader.getStrConnect());
    this.passportReader.onConnection();
  }

  /**
   * MODAL
   */
  openScanModal() {
    this.startPassportReader();
    this.progress = this.progressBar[0];
    this.cardImages = [];
    this.cardImageIndex = 0;
    this.resetHandleImage();
    this.resetAlert();
    this.utilService.playAudio(this.progress.audio);
  }

  closeScanModal() {
    this.passportReader.disConnect();
    this.closeCamera();
    this.sendGuestData();
    this.resetSelectedInstruction();
    this.resetHandleImage();
    this.closeAlert('errorModal');
  }

  /**
   * CLOSE CAMERA
   */
  closeCamera() {
    const event: HubModel = {
      receiver: PAGES.scanFace,
      message: ACTIONS.closeCamera,
    };
    this.hubService.sendEvent(event);
  }

  /**
   * PROCESS IMAGE
   */
  callImageApi(type: string, image: string) {
    const result = {};
    result[this.PASSPORT] = () => {
      return new Observable();
    };

    result[this.IDCARD] = this.ocrService.idImage(image);
    result[this.LICENSE] = this.ocrService.licenseImage(image);
    result[this.IDCARD_NEW] = this.ocrService.idImageNew(image);
    return result[type];
  }

  /**
   * Gn d liu api tr v
   */
  processImageResponse(type: string, data: any) {
    const result = {};
    result[this.PASSPORT] = () => {};
    result[this.IDCARD] = () => {
      this.guest = {
        birth: data['birth'] || '',
        expiry: '',
        guestName: data['name'] || '',
        passportNumber: data['id'] || '',
        passportType: '',
        address: data['add'] || '',
        sex: '',
        nationalityCode: '',
      };
    };
    result[this.LICENSE] = () => {
      this.guest = {
        birth: data['dob'] || '',
        expiry: '',
        guestName: data['name'] || '',
        passportNumber: data['id'] || '',
        passportType: '',
        address: data['add'] || '',
        sex: '',
        nationalityCode: data['nat'] || '',
      };
    };
    result[this.IDCARD_NEW] = () => {
      this.guest = {
        birth: data['dob'] || '',
        expiry: data['expire_date'] || '',
        guestName: data['name'] || '',
        passportNumber: data['id'] || '',
        passportType: '',
        address: data['add'] || '',
        sex: 'nam' || '',
        nationalityCode: data['nat'] || '',
      };
    };
    return result[type];
  }
  /** ALERT */
  resetAlert() {
    Object.keys(alert).forEach((key) => {
      this.alert[key] = {
        show: false,
        type: '',
        message: '',
      };
    });
  }

  showAlert(name = '') {
    if (name === 'errorModal') {
      this.hubService.error(this.alert[name].message); 
    } else {
      this.alert[name].show = true;
    }
    Object.keys(alert).forEach((key) => {
      if (name !== key) {
        this.alert[key] = {
          show: false,
          type: '',
          message: '',
        };
      }
    });
  }

  closeAlert(name = '') {
    this.alert[name] = {
      show: false,
      type: '',
      message: '',
    };
  }
}
