import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  OnDestroy,
  ViewEncapsulation,
} from '@angular/core';
import { NgbDate, NgbCalendar, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import * as moment from 'moment';
import { RoomAvailabilityService } from '../../services/room-availability/room-availability.service';
import { ResponseBody } from 'src/app/shared/models/response';
import { Router } from '@angular/router';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { ACTIONS } from 'src/app/shared/constant/event';
import { HubModel } from 'src/app/shared/models/hub.models';
import { PAGES, STEPS } from 'src/app/shared/constant/pages';
import { UtilityService } from 'src/app/shared/services/utility/utility.service';
import {
  faAngleDown,
  faAngleUp,
  faInfoCircle,
} from '@fortawesome/free-solid-svg-icons';
import { LangChangeEvent, TranslateService } from '@ngx-translate/core';
import { HubWalkinService } from 'src/app/shared/services/hub-walkin/hub-walkin.service';
import { mergeMap } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { BookingService } from '../../services/booking/booking.service';
import { RoomTypeInfoComponent } from '../room-type-info/room-type-info.component';

@Component({
  selector: 'app-availability-rooms',
  templateUrl: './availability-rooms.component.html',
  styleUrls: ['./availability-rooms.component.scss'],
})
export class AvailabilityRoomsComponent implements OnInit, OnDestroy {
  @Output() nextStep: EventEmitter<any> = new EventEmitter();
  @Output() sendData: EventEmitter<any> = new EventEmitter();

  private hubSubcription;
  public faAngleUp = faAngleUp;
  public faAngleDown = faAngleDown;
  public faInfoCircle = faInfoCircle;
  public bookingSteps = [];

  constructor(
    private modalService: NgbModal,
    private roomAvaiService: RoomAvailabilityService,
    public calendar: NgbCalendar,
    private router: Router,
    private hubService: HubService,
    public util: UtilityService,
    public translateService: TranslateService,
    public hubWalkin: HubWalkinService,
    public bookingService: BookingService
  ) {
    // this.fromDate = calendar.getToday();
    this.fromDate = calendar.getNext(calendar.getToday(), 'd', 1);
    this.toDate = calendar.getNext(calendar.getToday(), 'd', 2);

    this.lang.to = this.translateService.instant('roomAvail.selected.to');
    // console.log('lang to: ', this.lang.to);

    this.translateService.onLangChange.subscribe((event: LangChangeEvent) => {
      this.lang.to = this.translateService.instant('roomAvail.selected.to');
    });

    this.hubSubcription = this.hubService.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.availRooms) {
        return;
      }
      if (res.message === ACTIONS.nextStep) {
        this.changeToNextStep();
      } else if (res.message === ACTIONS.backStep) {
        this.backStep();
      }
    });
  }

  public fromDate: NgbDate;
  public toDate: NgbDate | null = null;
  public time = {
    arrival: '14:00',
    depart: '12:00',
  };
  public adultNum = 1;
  public childNum = 0;

  public roomAvaiData = [];
  public floorData = [];

  public selectedFloor = {
    floorID: 0,
    floorName: '',
    floorCode: '',
  };

  public selectedRoom = {
    roomName: '',
    roomTypeCode: '',
    floorName: '',
    buildingName: '',
  };

  public roomTypes = [];
  public selectedRoomTypeCodes = [];

  public error = {
    fetchRoomAvai: '',
  };

  public alertClosed = false;
  public lang = {
    to: '',
  };

  ngOnInit(): void {
    // this.fetchFloor();
    this.initData();
    this.hubWalkin.setStep(1);
  }

  ngOnDestroy() {
    this.hubSubcription.unsubscribe();
  }

  onSubmit(): void {
    this.changeToNextStep();
  }

  changeToNextStep(): void {
    if (!this.selectedRoom.roomName) {
      this.hubService.error(this.translateService.instant('msg.notSelectRoom'));
      return;
    }
    if (!moment(this.fromDate).isValid()) {
      this.hubService.error(
        this.translateService.instant('msg.notSelectArrival')
      );
      return;
    }
    if (!moment(this.toDate).isValid()) {
      this.hubService.error(
        this.translateService.instant('msg.notSelectDeparture')
      );
      return;
    }
    const transData = {
      ...this.selectedRoom,
      adult: this.adultNum,
      child: this.childNum,
      arrivalDate: moment(this.util.ngBDateToNormalDate(this.fromDate)).format(
        'YYYY-MM-DD'
      ),
      departDate: moment(this.util.ngBDateToNormalDate(this.toDate)).format(
        'YYYY-MM-DD'
      ),
      arrivalTime: this.time.arrival,
      departTime: this.time.depart,
    };
    this.router.navigate([`/${PAGES.walkin}/${STEPS.step2}`]);
    this.hubWalkin.setBookingData(transData);
  }

  initData() {
    this.hubService.setLoading(true);
    this.roomAvaiService
      .getRoomtypes()
      .pipe(
        mergeMap((res: ResponseBody) => {
          if (
            res.statusCode === 200 &&
            res.metadata &&
            res.metadata?.length > 0 &&
            res.metadata[0]
          ) {
            console.log('vao day');
            this.selectedRoomTypeCodes = [res.metadata[0].roomTypeCode];
          }
          this.roomTypes = res.metadata;
          return this.roomAvaiService.getBuildings();
        })
      )
      .subscribe(
        (res: any) => {
          console.log('hehehehe', res);
          if (
            res.statusCode === 200 &&
            res.metadata &&
            res.metadata?.length > 0 &&
            res.metadata[0].floors
          ) {
            this.floorData = res.metadata[0].floors.filter(
              (f) => f.floorCode !== '-1'
            );
            this.onSelectFloor(this.floorData[0]);
          } else {
            this.error.fetchRoomAvai = this.translateService.instant(
              'msg.errorHandlingRequest'
            );
          }
        },
        (err) => {
          console.log(err);
          this.error.fetchRoomAvai = this.translateService.instant(
            'msg.errorHandlingRequest'
          );
        }
      )
      .add(() => {
        this.hubService.setLoading(false);
      });
  }

  fetchFloor() {
    this.hubService.setLoading(true);

    this.roomAvaiService
      .getBuildings()
      .subscribe(
        (res: ResponseBody) => {
          if (
            res.statusCode === 200 &&
            res.metadata &&
            res.metadata?.length > 0 &&
            res.metadata[0].floors
          ) {
            this.floorData = res.metadata[0].floors.filter(
              (f) => f.floorCode !== '-1'
            );
            this.onSelectFloor(this.floorData[0]);
          } else {
            this.error.fetchRoomAvai = this.translateService.instant(
              'msg.errorHandlingRequest'
            );
          }
        },
        (err) => {
          this.error.fetchRoomAvai = this.translateService.instant(
            'msg.errorHandlingRequest'
          );
        }
      )
      .add(() => {
        this.hubService.setLoading(false);
      });
  }

  fetchRoomAvai() {
    this.error.fetchRoomAvai = '';
    this.resetSelectedRoom();

    setTimeout(() => {
      this.hubService.setLoading(true);
    }, 0);
    this.roomAvaiService
      .getRoomAvailabitlity(
        this.util.ngBDateToNormalDate(this.fromDate),
        this.util.ngBDateToNormalDate(this.toDate),
        true
      )
      .subscribe(this.fetchRoomAvailObserver())
      .add(() => {
        this.hubService.setLoading(false);
      });
  }

  fetchRoomAvailObserver() {
    return {
      next: (res: ResponseBody) => {
        if (res.statusCode === 200 && res.metadata) {
          console.log(this.selectedRoomTypeCodes);
          this.roomAvaiData = res.metadata
            .map((item) => ({
              ...item,
              buildingCode: `${item.buildingCode}`,
            }))
            .filter(
              (item) =>
                item.floorCode === this.selectedFloor.floorCode &&
                this.selectedRoomTypeCodes.includes(item.roomTypeCode)
            );
        } else {
          this.error.fetchRoomAvai = res.message;
        }
      },
      error: () => {
        this.error.fetchRoomAvai = this.translateService.instant(
          'msg.errorHandlingRequest'
        );
      },
    };
  }

  resetSelectedRoom() {
    this.selectedRoom = {
      buildingName: '',
      floorName: '',
      roomName: '',
      roomTypeCode: '',
    };
  }

  onSelectRoom(room: any) {
    this.selectedRoom = { ...room };
  }

  onSelectFloor(floor) {
    this.selectedFloor = { ...floor };
    this.fetchRoomAvai();
  }

  clickRoomType(roomtype: any) {
    if (this.selectedRoomTypeCodes.includes(roomtype.roomTypeCode)) {
      this.selectedRoomTypeCodes = this.selectedRoomTypeCodes.filter(
        (code) => code !== roomtype.roomTypeCode
      );
    } else {
      this.selectedRoomTypeCodes.push(roomtype.roomTypeCode);
    }
    this.fetchRoomAvai();
  }

  showRoomType(roomtype: any) {
    const modalRef = this.modalService.open(RoomTypeInfoComponent, {
      size: 'lg',
      backdrop: 'static',
      centered: true,
    });
    modalRef.componentInstance.roomType = roomtype;
  }

  clickStep(num) {
    if (this.bookingSteps.includes(num)) {
      this.bookingSteps = this.bookingSteps.filter((item) => item <= num);
    } else {
      this.bookingSteps = Array.from({ length: num }, (_, i) => i + 1);
    }
  }
  /**
   * Back Step
   */
  backStep() {
    this.router.navigateByUrl('/options');
  }
  /**
   * Adult/child
   */
  increaseAdultNum() {
    this.adultNum += 1;
  }

  decreaseAdultNum() {
    if (this.adultNum > 1) {
      this.adultNum -= 1;
    }
  }

  increaseChildNum() {
    this.childNum += 1;
  }

  decreaseChildNum() {
    if (this.childNum > 0) {
      this.childNum -= 1;
    }
  }

  openTimePicker(content) {
    this.modalService.open(content, {
      centered: true,
      windowClass: 'date-picker-range-modal',
    });
  }

  closeTimePicker() {
    this.modalService.dismissAll();
  }
  /**
   * ARR DEPT
   */
  onDateSelection({ fromDate, toDate }) {
    this.fromDate = fromDate;
    this.toDate = toDate;

    if (this.fromDate && this.toDate) {
      this.fetchRoomAvai();
    }
  }

  displayArrDeptDate() {
    return `${this.util.displayDateDmy(
      this.util.ngBDateToNormalDate(this.fromDate)
    )} ${this.lang.to} ${this.util.displayDateDmy(
      this.util.ngBDateToNormalDate(this.toDate)
    )}`;
  }

  displayArrDeptTime() {
    return `${this.time.arrival} - ${this.time.depart}`;
  }

  displayNumNight() {
    return this.util.displayNumNight(
      this.util.dateYmd(this.util.ngBDateToNormalDate(this.fromDate)),
      this.util.dateYmd(this.util.ngBDateToNormalDate(this.toDate))
    );
  }
  /**
   * SCROLL BUTTON
   */
  scrollUp() {
    const container = document.getElementById('room-list-scroll');
    this.sideScroll(container, 'up', 1, 150, 10);
  }

  scrollDown() {
    const container = document.getElementById('room-list-scroll');
    this.sideScroll(container, 'down', 1, 150, 10);
  }

  sideScroll(element, direction, speed, distance, step): void {
    let scrollAmount = 0;
    let slideTimer = window.setInterval(function () {
      if (direction == 'up') {
        element.scrollTop -= step;
      } else {
        element.scrollTop += step;
      }
      scrollAmount += step;
      if (scrollAmount >= distance) {
        window.clearInterval(slideTimer);
      }
    }, speed);
  }
}
