import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  OnDestroy,
  Input,
} from '@angular/core';
import * as moment from 'moment';
import { faRedo, faTruckMonster } from '@fortawesome/free-solid-svg-icons';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { HubModel } from 'src/app/shared/models/hub.models';
import { PAGES, STEPS } from 'src/app/shared/constant/pages';
import { ACTIONS } from 'src/app/shared/constant/event';
import { UtilityService } from 'src/app/shared/services/utility/utility.service';
import { HubWalkinService } from 'src/app/shared/services/hub-walkin/hub-walkin.service';
import { BookingService } from '../../services/booking/booking.service';
import { mergeMap } from 'rxjs/operators';
import { ResponseBody } from 'src/app/shared/models/response';
import { RoomAvailabilityService } from '../../services/room-availability/room-availability.service';
import { throwError } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-booking-details',
  templateUrl: './booking-details.component.html',
  styleUrls: ['./booking-details.component.scss'],
})
export class BookingDetailsComponent implements OnInit, OnDestroy {
  @Output() nextStep: EventEmitter<any> = new EventEmitter();
  @Output() backStep: EventEmitter<any> = new EventEmitter();
  @Output() refreshRoomAvai: EventEmitter<any> = new EventEmitter();

  private hubSubcription;
  public faRedo = faRedo;

  public bookingData: any;
  public totalRateData: any;
  public roomAvaiData = [];
  public error = '';

  constructor(
    private hubService: HubService,
    public utilService: UtilityService,
    private hubWalkin: HubWalkinService,
    private bookingService: BookingService,
    private roomAvaiService: RoomAvailabilityService,
    private translateService: TranslateService,
    private router: Router,
  ) {
    this.hubSubcription = this.hubService.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.bookingDetails) {
        return;
      }
      if (res.message === ACTIONS.nextStep) {
        this.changeToNextStep();
      } else if (res.message === ACTIONS.backStep) {
        this.changeToPreviousStep();
      }
    });
  }

  ngOnInit(): void {
    this.hubWalkin.setStep(2);
    this.bookingData = {...this.hubWalkin.getBookingData()};
    this.initData(this.bookingData);
  }

  ngOnDestroy(): void {
    this.hubSubcription.unsubscribe();
  }

  initData(guestData: any) {
    setTimeout(() => {
      this.hubService.setLoading(true);
    }, 0);
    this.bookingData = { ...this.bookingData, ...guestData };
    this.bookingService
      .getTotalRate({
        ArrivalDate: this.bookingData.arrivalDate || moment(new Date()).format('YYYY-MM-DD'),
        DepartDate: this.bookingData.departDate || moment(new Date()).format('YYYY-MM-DD'),
        ArrivalTime: this.bookingData.arrivalTime,
        DepartTime: this.bookingData.departTime,
        RoomBocked: this.bookingData.roomCode,
        RoomType: this.bookingData.roomTypeCode,
        NoOfAdults: this.bookingData.adult,
        NoOfChild: this.bookingData.child,
      })
      .pipe(
        mergeMap((res: ResponseBody) => {
          if (res.metadata && res.metadata[0]) {
            this.totalRateData = res.metadata[0];
            return this.roomAvaiService.getRoomAvailabitlity(
              this.bookingData.arrivalDate,
              this.bookingData.departDate,
              true
            );
          } else {
            throwError({
              code: '',
              message: this.translateService.instant('msg.errorGetTotalRate'),
              useToast: faTruckMonster,
            });
          }
        })
      )
      .subscribe(
        (res: ResponseBody) => {
          if (res.statusCode === 200 && res.metadata) {
            this.roomAvaiData = res.metadata
              .map((item) => ({
                ...item,
                buildingCode: `${item.buildingCode}`,
              }))
              // .filter(
              //   (item) => item?.roomTypeCode === this.bookingData.roomTypeCode
              // );
            if (this.roomAvaiData[0]) {
              this.changeRoomCode(this.roomAvaiData[0].roomCode);
            }
          } else {
            this.error = res.message || '';
            throwError({
              code: '',
              message: this.translateService.instant('msg.errorGetTotalRate'),
              useToast: false,
            });
          }
        },
        (err: any) => {
          if (err.useToast) {
            this.hubService.error(
              err?.message ||
                this.translateService.instant('msg.errorHandlingRequest')
            );
          }
        }
      ).add(() => {
        this.hubService.setLoading(false);
      });
  }

  changeRoomCode(roomCode: string) {
    this.bookingData.roomCode = roomCode;
  }

  accept() {
    this.changeToNextStep();
  }

  changeToNextStep() {
    // this.nextStep.emit({
    //   ...this.bookingData,
    // });
    this.hubWalkin.setBookingData(this.bookingData);
    this.router.navigate([`/${PAGES.checkinQr}/${STEPS.step3}`]);
  }

  changeToPreviousStep() {
    this.backStep.emit();
  }

  onSelectRoom(room: any) {
    this.changeRoomCode(room.roomCode);
  }
  
  refresh() {
    this.initData(this.bookingData);
  }

  /**
   * DISPLAY
   */
  displayArrival(): string {
    return moment(this.bookingData.arrivalDate).format('DD/MM/yyyy') || '';
  }

  displayDepart(): string {
    return moment(this.bookingData.departDate).format('DD/MM/yyyy') || '';
  }

  displayNumNight(): string {
    if (this.bookingData.arrivalDate === this.bookingData.departDate) {
      return `1`;
    }
    const start = moment(this.bookingData.arrivalDate);
    const end = moment(this.bookingData.departDate);
    return `${end.startOf('day').diff(start.startOf('day'), 'd')}`;
  }

  displayTotal() {
    const total =
      (this.totalRateData?.rate || 0) +
      (this.totalRateData?.tax1 || 0) +
      (this.totalRateData?.tax2 || 0);
    return total || 0;
  }
}
