import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MenuComponent } from './modules/checkin/components/menu/menu.component';
import { PAGES, STEPS } from './shared/constant/pages';
import { FlowCheckinComponent } from './modules/checkin/components/flow-checkin/flow-checkin.component';
import { FlowBookingComponent } from './modules/checkin/components/flow-booking/flow-booking.component';
import { WelcomeComponent } from './modules/checkin/components/welcome/welcome.component';
import { AvailabilityRoomsComponent } from './modules/checkin/components/availability-rooms/availability-rooms.component';
import { ScanIdentityCardComponent } from './modules/checkin/components/scan-identity-card/scan-identity-card.component';
import { BookingCreateComponent } from './modules/checkin/components/booking-create/booking-create.component';
import { BookingDoneComponent } from './modules/checkin/components/booking-done/booking-done.component';
import { WalkinComponent } from './layout/walkin/walkin.component';
import { FlowDetectComponent } from './modules/checkin/components/flow-detect/flow-detect.component';
import { ScanQrComponent } from './modules/checkin/components/scan-qr/scan-qr.component';
import { BookingDetailsComponent } from './modules/checkin/components/booking-details/booking-details.component';
import { BookingCodeComponent } from './modules/checkin/components/booking-code/booking-code.component';


const routes: Routes = [
  {
    path: PAGES.flowCheckin,
    component: FlowCheckinComponent,
  },
  {
    path: PAGES.flowBooking,
    component: FlowBookingComponent,
  },
  {
    path: PAGES.options,
    component: MenuComponent,
  },
  {
    path: PAGES.welcome,
    component: WelcomeComponent,
  },
  {
    path: '',
    redirectTo: PAGES.welcome,
    pathMatch: 'full',
  },
  {
    path: PAGES.walkin,
    component: WalkinComponent,
    children: [
      {
        path: STEPS.step1,
        component: AvailabilityRoomsComponent,
      },
      {
        path: STEPS.step2,
        component: ScanIdentityCardComponent
      },
      {
        path: STEPS.step3,
        component: BookingCreateComponent,
      },
      {
        path: STEPS.step4,
        component: BookingDoneComponent,
      },
      {
        path: STEPS.detect,
        component: FlowDetectComponent,
      }
    ]
  },
  {
    path: PAGES.checkinQr,
    component: WalkinComponent,
    children: [
      {
        path: STEPS.step1,
        component: ScanQrComponent,
      },
      {
        path: STEPS.step2,
        component: BookingDetailsComponent
      },
      {
        path: STEPS.step3,
        component: ScanIdentityCardComponent,
      },
      {
        path: STEPS.step4,
        component: BookingDoneComponent,
      },
      {
        path: STEPS.detect,
        component: FlowDetectComponent,
      }
    ]
  },
  {
    path: PAGES.checkinCode,
    component: WalkinComponent,
    children: [
      {
        path: STEPS.step1,
        component: BookingCodeComponent,
      },
      {
        path: STEPS.step2,
        component: BookingDetailsComponent
      },
      {
        path: STEPS.step3,
        component: ScanIdentityCardComponent,
      },
      {
        path: STEPS.step4,
        component: BookingDoneComponent,
      },
      {
        path: STEPS.detect,
        component: FlowDetectComponent,
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{ useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
