import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { NgbDate } from '@ng-bootstrap/ng-bootstrap/datepicker/ngb-date';
import { TranslateService } from '@ngx-translate/core';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root',
})
export class UtilityService {
  private prefixAudio = `assets/audio`;
  private cachedAudio;

  constructor(private translate: TranslateService, private router: Router) {}

  /**
   * Bt file audio
   */
  playAudio(fileName = '') {
    const path = this.generateAudioPath(fileName);

    if (this.cachedAudio) {
      this.cachedAudio.pause();
    }

    this.cachedAudio = new Audio(path);
    this.playAudioFile();
  }

  /**
   *
   * @param arr string
   * @param dept string
   * @returns S m
   */
  displayNumNight(arr: any, dept: any): string {
    if (arr === dept) {
      return `1`;
    }
    const start = moment(arr);
    const end = moment(dept);
    const result = end.diff(start, 'd');
    return `${result || ''}`;
  }

  displayDateDmy(date): string {
    if (moment(date).isValid()) {
      return moment(date).format('DD/MM/YYYY') || ''; 
    }
    return '';
  }

  dateYmd(date): string {
    if (moment(date).isValid()) {
      return moment(date).format('YYYY/MM/DD') || ''; 
    }
    return '';
  }

  ngBDateToNormalDate(date: NgbDate) {
    if (date) {
      return new Date(date.year, date.month - 1, date.day); 
    }
    return null;
  }

  /**
   * Bt li file audio gn nht va dng
   */
  replayAudio() {
    this.playAudioFile();
  }

  private playAudioFile() {
    this.cachedAudio.autoplay = true;
    this.cachedAudio.play();
  }

  private generateAudioPath(fileName = '') {
    const langCode = this.translate.currentLang;
    const path = `${this.prefixAudio}/${langCode}/${fileName}`;
    return path;
  }
  /**
   * nh dng tin
   */
  public formatMoney(num) {
    return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(num).replace('', '') ;
  }
  /**
   * Get Location
   */
  public getCurrentPage(route = '') {
    const splited = this.splitRoute(route || this.router.url);
    const step = splited.pop();
    const page = splited.pop();
    return page;
  }

  public getCurrentStep(route = '') {
    const splited = this.splitRoute(route || this.router.url);
    const step = splited.pop();
    return step;
  }

  public splitRoute(route = '') {
    return (route || this.router.url).split(/[\/?]/);
  }


}
