import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ConfigureService } from 'src/app/config/configure/configure.service';

@Injectable({
  providedIn: 'root'
})
export class DispenserService {
  private serviceUrl = '';
  constructor(private http: HttpClient) {
    this.serviceUrl = ConfigureService.config.url.service;
  }

  /**
   * a th ti v tr ghi
   */
  public moveToRfPos() {
    const endpoint = `${this.serviceUrl}/api/dispense/rf-pos`;
    return this.http.post(endpoint, {});
  }

  /**
   * Nh th
   */
  public eject() {
    const endpoint = `${this.serviceUrl}/api/dispense/eject`;
    return this.http.post(endpoint, {});
  }

}
