import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import Stepper from 'bs-stepper';
import { ACTIONS } from 'src/app/shared/constant/event';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { HubModel } from 'src/app/shared/models/hub.models';
import { PAGES } from 'src/app/shared/constant/pages';
import { UtilityService } from 'src/app/shared/services/utility/utility.service';
import { AUDIO_FILES } from 'src/app/shared/constant/audio-files';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';
import { FlowDetectComponent } from '../flow-detect/flow-detect.component';
import { BookingService } from '../../services/booking/booking.service';
import { ResponseBody } from 'src/app/shared/models/response';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-flow-booking',
  templateUrl: './flow-booking.component.html',
  styleUrls: ['./flow-booking.component.scss'],
})
export class FlowBookingComponent implements OnInit, OnDestroy {
  constructor(
    private hubService: HubService,
    private utilService: UtilityService,
    private router: Router,
    private bookingService: BookingService,
    private translateService: TranslateService,
  ) {
    this.subcribeHub();
  }

  @ViewChild('flowDetectComp') flowDetectComp: FlowDetectComponent;

  public showFlowDetect = false;
  public showFooter = true;
  public isDoneFlow = true;
  public errorMessageCreateBooking = '';

  private stepper: Stepper;
  public bookingData:any = {};
  public totalRateData: any = {};

  private hubSubcription = null;

  next() {
    this.stepper.next();
  }

  back() {
    const current = this.getActiveStep();
    const backToOptionsScreen = [PAGES.availRooms, PAGES.bookingDone];
    if (this.showFlowDetect) {
      this.changeStepInsideFlowDetect(this.flowDetectComp.progress.key);
      return;
    }
    if (backToOptionsScreen.includes(current)) {
      this.router.navigateByUrl('/options');
      return;
    }
    this.stepper.previous();
  }

  ngOnInit() {
    setTimeout(() => {
      this.initStepper();
      this.addListener();
    }, 0);
  }

  ngOnDestroy(): void {
    this.hubSubcription.unsubscribe();
  }

  subcribeHub(): void {
    this.hubSubcription = this.hubService.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.flowBooking) {
        return;
      }
      if (res.message === ACTIONS.goBack) {
        this.back();
      }
    });
  }

  initStepper() {
    const linear = environment.production;

    this.stepper = new Stepper(
      document.querySelector('#step-new-reservation'),
      {
        linear: linear,
        animation: true,
      }
    );
  }

  addListener() {
    const stepperEl = document.querySelector('#step-new-reservation');

    stepperEl.addEventListener('shown.bs-stepper', (event: any) => {
      const activeStep = this.getActiveStep();

      this.actionController(activeStep, event.detail);
    });
  }

  actionController(activeStep: string, detail: any) {
    if (activeStep === PAGES.availRooms) {
      this.utilService.playAudio(AUDIO_FILES.availRoomIntro);
      this.showFooter = false;
    } else if (
      detail &&
      detail.to === 1 &&
      (detail.from === 2 || detail.from === 1)
    ) {
      this.showFooter = false;
      this.openFlowDetect();
    } else if (activeStep === PAGES.bookingCreate) {
      this.utilService.playAudio(AUDIO_FILES.bookingCreateIntro);
      this.showFooter = true;
    } else if (activeStep === PAGES.bookingDone) {
      this.showFooter = false;
      const audioFile = this.isDoneFlow ? AUDIO_FILES.bookingDoneIntro : AUDIO_FILES.bookingDoneError;
      this.utilService.playAudio(audioFile);
    }
  }

  receiveBookingData(bookingData: any, isNext = false) {
    console.log({ bookingData });
    this.bookingData = { ...bookingData };
    if (isNext) {
      this.hubService.setLoading(true);
      this.bookingService
        .getTotalRate({
          ArrivalDate: this.bookingData.arrivalDate,
          DepartDate: this.bookingData.departDate,
          ArrivalTime: this.bookingData.arrivalTime,
          DepartTime: this.bookingData.departTime,
          RoomBocked: this.bookingData.roomCode,
          RoomType: this.bookingData.roomTypeCode,
          NoOfAdults: this.bookingData.adult,
          NoOfChild: this.bookingData.child,
        })
        .subscribe(
          (res: ResponseBody) => {
            if (res.metadata && res.metadata[0]) {
              this.totalRateData = res.metadata[0];
              this.next();
            } else {
              this.hubService.error(this.translateService.instant('msg.errorGetTotalRate'));
            }
          },
          (err) => {
            this.hubService.error(this.translateService.instant('msg.errorGetTotalRate'));
          }
        )
        .add(() => {
          this.hubService.setLoading(false);
        });
    }
  }
  /**
   * Bt popup chn giy t khi vo mn hnh Scan
   */
  openFlowDetect() {
    console.log('open flow detect');
    this.showFlowDetect = true;

    setTimeout(() => {
      const event: HubModel = {
        receiver: PAGES.flowDetect,
        message: ACTIONS.openScanModal,
      };

      this.hubService.sendEvent(event);
    }, 0);
  }

  /**
   *
   * X l khi to booking b li
   */
  onCreateBooking({ isDone = true, msg = '' }) {
    this.isDoneFlow = isDone;
    this.errorMessageCreateBooking = msg;
    this.next();
  }
  /**
   * Reset Flow
   */
  resetFlow() {
    this.stepper.to(0);
  }
  /**
   *
   * Close Flow Detect
   */
  closeFlowDetect(guestData: any) {
    console.log('close flow detect: ', guestData, this.bookingData);
    this.bookingData = { ...this.bookingData, ...guestData };
    this.showFlowDetect = false;

    setTimeout(() => {
      this.initStepper();
      this.addListener();
      this.stepper.to(2);
    }, 0);
  }
  /**
   * 
   * UPDATE STEP TRONG FLOW DETECT
   */
   changeStepInsideFlowDetect(detectStep: string) {
    if (detectStep === 'type') {
      this.showFlowDetect = false;
      this.closeFlowDetect(null); 
    } else {
      this.openFlowDetect();
    }
  }
  /**
   * NAVIGATION
   */
  goToPreviousStep() {
    const componentName = this.getActiveStep();
    const type = ACTIONS.backStep;
    const data: HubModel = {
      receiver: componentName,
      message: type,
    };
    this.hubService.sendEvent(data);
  }

  gotoNextStep() {
    const componentName = this.getActiveStep();
    const type = ACTIONS.nextStep;
    const data: HubModel = {
      receiver: componentName,
      message: type,
    };
    this.hubService.sendEvent(data);
  }

  getActiveStep() {
    const bsStepperContent = document.getElementById('bsStepperContent');
    const activeStep = bsStepperContent?.querySelector(
      '.dstepper-block.active'
    );
    return activeStep?.getAttribute('data-name') || '';
  }
}
