import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import { ConfigureService } from 'src/app/config/configure/configure.service';

@Injectable({
  providedIn: 'root'
})
export class OcrService {
  private ocrUrl = '';
  constructor(private http: HttpClient) {
    this.ocrUrl = ConfigureService.config.url.ocr;
  }

  /**
   * Ly thng tin t nh cmnd c
   */
  public idImage(base64: string) {
    const endpoint = `${this.ocrUrl}/predict/id_image`;

    const file = this.dataURLtoFile(base64, 'file.jpg');
    
    const formData = new FormData();
    formData.append('file', file);

    return this.http.post(endpoint, formData);
  }

  /**
   * Ly thng tin t bng li xe
   */
  public licenseImage(base64: string) {
    const endpoint = `${this.ocrUrl}/predict/license_image`;

    const file = this.dataURLtoFile(base64, 'file.jpg');
    
    const formData = new FormData();
    formData.append('file', file);

    return this.http.post(endpoint, formData);
  }

  /**
   * Ly thng tin t cn cc cng dn
   */
  public idImageNew(base64: string) {
    const endpoint = `${this.ocrUrl}/predict/id_image_new`;

    const file = this.dataURLtoFile(base64, 'file.jpg');
    
    const formData = new FormData();
    formData.append('file', file);

    return this.http.post(endpoint, formData);
  }

  /**
   * So khp mt
   */
  public faceEmbedding(source: string, input: string) {
    const endpoint = `${this.ocrUrl}/predict/face_embedding`;

    const sourceFile = this.dataURLtoFile(source, 'source.jpg');
    const inputFile = this.dataURLtoFile(input, 'input.jpg');

    const formData = new FormData();
    formData.append('source_file', sourceFile);
    formData.append('input_file', inputFile);

    return this.http.post(endpoint, formData);
  }

  /**
   * 
   * Convert base64 sang File
   *  
   */
  private dataURLtoFile(dataurl, filename): File {
    var arr = dataurl.split(','),
      mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);

    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }

    return new File([u8arr], filename, { type: mime });
  }
}
