import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class RoomAvailabilityService {
  constructor(private http: HttpClient) {}

  /**
   * Ly ds phng trng
   */
  public getRoomAvailabitlity(
    arrivalDate: any,
    departDate: any,
    formatDate?: boolean
  ) {
    if (formatDate) {
      arrivalDate = moment(arrivalDate).format('yyyy-MM-DD');
      departDate = moment(departDate).format('yyyy-MM-DD');
    }

    return this.http.post('api/room/get-room-availability', {
      arrivalDate,
      departDate
    });
  }

  /**
   * 
   * Ly List Building
   * Trong Building c floor
   */
  public getBuildings() {
    return this.http.get('api/building/get-buildings');
  }

  /**
   * 
   * Ly List Roomtypes
   */
   public getRoomtypes() {
    return this.http.get('api/room/get-room-types');
  }
}
