import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SearchBookingModel } from '../../models/booking/search-booking.model';
import { CreateBookingModel } from '../../models/booking/create-booking.model';
import { GetTotalRateModel as GetTotalRateModel } from '../../models/booking/get-total-rate.model';
import * as moment from 'moment';
import { CheckInModel } from '../../models/booking/check-in.model';

@Injectable({
  providedIn: 'root',
})
export class BookingService {
  constructor(private http: HttpClient) {}

  createBooking(body: any) {
    const data: CreateBookingModel = {
      guestName: '',
      address: '',
      phone: '',
      arrivalDate: '',
      departDate: '',
      arrivalTime: '',
      departTime: '',
      adult: 0,
      child: 0,
      buildingCode: '',
      roomCode: '',
      roomTypeCode: '',
      rateCode: '',
      seasonCode: '',
      isRateOverride: 'N',
      rateOverrideValue: 0,
      isNetPrice: 'N',
      useMeal: 'N',
      mealCode: '',
      taRecLoc: '',
      remark: '',
      reservationType: '',
      paymentMethod: '',
      paymentAmount: 0,
    };

    Object.keys(data).forEach((key) => {
      data[key] = body[key];
    });

    const endpoint = 'api/booking/create-booking';
    return this.http.post(endpoint, data);
  }

  searchBooking(body: SearchBookingModel) {
    const endpoint = `api/booking/search-booking`;
    return this.http.post(endpoint, body);
  }

  getTotalRate(body: GetTotalRateModel) {
    const arrDate = moment(`${body.ArrivalDate}`).format('MM/DD/YYYY');
    const dptDate = moment(`${body.DepartDate}`).format('MM/DD/YYYY');

    const arrTime = moment(`${body.ArrivalTime}`, 'HH:mm', true)
      ? body.ArrivalTime
      : moment(`${body.ArrivalTime}`).format('HH:mm');
    const dptTime = moment(`${body.DepartTime}`, 'HH:mm', true)
      ? body.DepartTime
      : moment(`${body.DepartTime}`).format('HH:mm');

    const endpoint = `api/room/get-total-rate`;
    const data: GetTotalRateModel = {
      Season: 'ALL',
      RateCode: 'BAR',
      RateOverride: 'N',
      IsNetPrice: 'N',
      ...body,
      ArrivalDate: arrDate,
      DepartDate: dptDate,
      ArrivalTime: arrTime,
      DepartTime: dptTime,
    };
    return this.http.post(endpoint, data);
  }

  checkin(body: CheckInModel) {
    const endpoint = `api/booking/checkin`;
    return this.http.post(endpoint, body);
  }
}
