import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  OnDestroy,
  Input
} from '@angular/core';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { HubModel } from 'src/app/shared/models/hub.models';
import { PAGES, STEPS } from 'src/app/shared/constant/pages';
import { ACTIONS } from 'src/app/shared/constant/event';
import { TranslateService } from '@ngx-translate/core';
import { environment } from 'src/environments/environment';
import { HubWalkinService } from 'src/app/shared/services/hub-walkin/hub-walkin.service';
import { Router } from '@angular/router';
import { UtilityService } from 'src/app/shared/services/utility/utility.service';
import { BookingService } from '../../services/booking/booking.service';
import { mergeMap } from 'rxjs/operators';
import { DispenserService } from 'src/app/shared/services/dispenser/dispenser.service';
import { throwError } from 'rxjs';
import * as moment from 'moment';
import { LockCardModel } from 'src/app/shared/models/lock-card.model';
import { LockCardService } from 'src/app/shared/services/lock-card/lock-card.service';

@Component({
  selector: 'app-scan-identity-card',
  templateUrl: './scan-identity-card.component.html',
  styleUrls: ['./scan-identity-card.component.scss']
})
export class ScanIdentityCardComponent implements OnInit, OnDestroy {
  @Output() sendGuestInfo: EventEmitter<any> = new EventEmitter();
  @Output() backStep: EventEmitter<any> = new EventEmitter();
  @Output() detectAgain: EventEmitter<any> = new EventEmitter();

  
  public alert = {
    errorModal: {
      show: false,
      type: '',
      message: ''
    },
    successModal: {
      show: false,
      type: '',
      message: ''
    },
    successOutside: {
      show: false,
      type: '',
      message: ''
    }
  };
  private hubSubcription = [];
  public guestData: any = {
    guestName: '',
    address: '',
    birth: '',
    expiry: '',
    passportNumber: '',
    passportType: '',
    sex: '',
    nationalityCode: ''
  };
  public bookingData:any = {};

  constructor(
    private hubService: HubService,
    private translateService: TranslateService,
    private hubWalkin: HubWalkinService,
    private router: Router,
    private utilService: UtilityService,
    private bookingService: BookingService,
    private dispenserService: DispenserService,
    private lockCardService: LockCardService,
  ) {
    const sub1 = this.hubService.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.scanIdCard) {
        return;
      }
      this.processHubEvent(res);
    });
    this.hubSubcription = [sub1];
  }

  ngOnInit(): void {
    this.guestData = {...this.hubWalkin.bookingData};
    const page = this.utilService.getCurrentPage();
    console.log('init scan id card: ', page);
    switch (page) {
      case PAGES.walkin:
        this.hubWalkin.setStep(2);   
        break;
      case PAGES.checkinQr:
        this.hubWalkin.setStep(3);
        break;
      default:
        break;
    }
  }

  ngOnDestroy(): void {
    this.hubSubcription.forEach(item => item.unsubscribe());
  }

  sendGuestData() {
    // this.sendGuestInfo.emit(this.guest);
  }

  processHubEvent(event: HubModel) {
    if (event.message === ACTIONS.nextStep) {
      this.changeToNextStep();
    } else if (event.message === ACTIONS.backStep) {
      this.changeToPreviousStep();
    }
  }

  handleNextStep() {
    const page = this.utilService.getCurrentPage();
    if (page === PAGES.walkin) {
      this.changeToNextStep();
      return;
    }
    this.processCard();
  }

  changeToNextStep() {
    const page = this.utilService.getCurrentPage();
    let step = '';
    switch (page) {
      case PAGES.checkinQr:
        step = STEPS.step4;
        break;
      case PAGES.walkin:
        step = STEPS.step3;
        break;
      default:
        break;
    }
    if (!environment.production) {
      this.hubWalkin.setBookingData({...this.guestData});
      this.router.navigate([`/${page}/${step}`]);
      return;
    }
    if ((this.guestData.passportNumber || '') !== '') {
      this.hubWalkin.setBookingData({...this.guestData});
      this.router.navigate([`/${page}/${step}`]);
    } else {
      this.hubService.error(this.translateService.instant('msg.plsScan'));
    }
  }

  changeToPreviousStep() {
    this.backStep.emit();
  }

  useOtherCard() {
    // this.detectAgain.emit();
    const page = this.utilService.getCurrentPage();
    this.router.navigate([`/${page}/${STEPS.detect}`]);
  }

  processCard() {
    // this.bookingData = { ...this.bookingData, ...guestData };
    this.hubWalkin.setBookingData(this.guestData);
    console.log('process card | booking data: ', this.guestData);

    this.hubService.setLoading(true);
    this.bookingService
      .checkin({
        resvNo: this.guestData?.resvNo,
        roomCode: this.guestData?.roomCode,
      })
      .pipe(
        mergeMap((res: any) => {
          if (res.statusCode === 200) {
            return this.dispenserService.moveToRfPos();
          } else {
            const message = this.translateService.instant('msg.errorCheckin');
            return throwError({ code: res.statusCode, message });
          }
        }),
        mergeMap((res: any) => {
          if (res.code === 0) {
            return this.writeCard();
          } else {
            return throwError({ code: res.code, message: res.message });
          }
        }),
        mergeMap((res: any) => {
          if (res.code === 0) {
            return this.dispenserService.eject();
          } else {
            return throwError({ code: res.code, message: res.message });
          }
        })
      )
      .subscribe(
        (res: any) => {
          if (res.code === 0) {
            // this.onVerify({ isDone: true });
            this.hubWalkin.isDoneFlow = true;
          } else {
            this.hubService.error(res.message);
            // this.onVerify({ isDone: false });
            this.hubWalkin.isDoneFlow = false;
          }
        },
        ({ code, message }) => {
          if (!environment.production) {
            this.changeToNextStep();
            return;
          }
          this.hubService.error(message);
          // this.onVerify({ isDone: false });
          this.hubWalkin.isDoneFlow = false;
        }
      )
      .add(() => {
        this.hubService.setLoading(false);
      });
  }
  /**
   *
   * Ghi th
   */
  writeCard() {
    const lockArrDate = moment(this.guestData['arrivalDate']).format(
      'yyyy-MM-DD'
    );
    const lockDeptDate = moment(this.guestData['departDate']).format(
      'yyyy-MM-DD'
    );

    const lockArrTime = moment(this.guestData['arrivalTime'], 'HH:mm').format(
      'HH:mm:ss'
    );
    const lockDeptTime = moment(this.guestData['departTime'], 'HH:mm').format(
      'HH:mm:ss'
    );

    const body: LockCardModel = {
      room: this.guestData['roomCode'],
      building: this.guestData['buildingCode'],
      arrival: `${lockArrDate} ${lockArrTime}`,
      departure: `${lockDeptDate} ${lockDeptTime}`,
    };

    return this.lockCardService.writeCard(body);
  }

}
