import { Component } from '@angular/core';
import { HubService } from './shared/services/hub/hub.service';
import { HubModel } from './shared/models/hub.models';
import { PAGES } from './shared/constant/pages';
import { ACTIONS } from './shared/constant/event';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalErrorComponent } from './shared/components/modal-error/modal-error/modal-error.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})


export class AppComponent {
  title = 'kiosk-checkin';
  public loading = false;
  /**
   * 
   */
  constructor(private hub: HubService, private modalServie: NgbModal,) {
    this.hub.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.root) {
        return;
      }
      if (res.message === ACTIONS.loading) {
        this.loading = res.data || false;
      } else if (res.message === ACTIONS.showErrorModal) {
        this.displayError(null, res.data);
      }
    });
  }

  displayError(code: any, msg: string) {
    const ref = this.modalServie.open(ModalErrorComponent, {
      size: 'xl',
      centered: true
    });
    ref.componentInstance.errorCode = code;
    ref.componentInstance.errorMessage = msg;
  }
}


