import { Component, OnDestroy, OnInit } from '@angular/core';
import Stepper from 'bs-stepper';
import { HubWalkinService } from 'src/app/shared/services/hub-walkin/hub-walkin.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-walkin',
  templateUrl: './walkin.component.html',
  styleUrls: ['./walkin.component.scss']
})
export class WalkinComponent implements OnInit, OnDestroy {

  private stepper: Stepper;
  private hubSubcription;
  
  public isShowStepper = false;
  
  constructor(public hubWalkin: HubWalkinService) { 
    this.hubSubcription = hubWalkin.onStep().subscribe((step) => {
      console.log('step: ', step);
      this.stepper.to(step);
    });
  }
  ngOnDestroy(): void {
    // this.hubSubcription.unsubscribe();
  }

  ngOnInit(): void {
    this.initStepper();
    console.log('on init');
  }


  initStepper() {
    const linear = environment.production;
    console.log(this.hubWalkin.isShowStepper);
    this.hubWalkin.setIsShowStepper(true);
    this.stepper = new Stepper(
      document.querySelector('#stepper-checkin'),
      {
        linear: linear,
        animation: true,
      }
    );
    // this.stepper.to(this.hubWalkin.getStep());
    // this.stepper.to(0);
  }

}
