import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-room-type-info',
  templateUrl: './room-type-info.component.html',
  styleUrls: ['./room-type-info.component.scss']
})
export class RoomTypeInfoComponent implements OnInit {

  @Input() roomType:any;

  constructor(public activeModal: NgbActiveModal) { }

  ngOnInit() {

  }

  roomTypeImage() {
    const file = this.roomType.roomTypeCode.toUpperCase();
    return `assets/images/${file}.jpg`;
  }

}
