import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  Input,
  OnDestroy,
} from '@angular/core';
import { BookingService } from '../../services/booking/booking.service';
import * as moment from 'moment';
import { ResponseBody } from 'src/app/shared/models/response';
import { HubService } from 'src/app/shared/services/hub/hub.service';
import { HubModel } from 'src/app/shared/models/hub.models';
import { PAGES, STEPS } from 'src/app/shared/constant/pages';
import { ACTIONS } from 'src/app/shared/constant/event';
import { LockCardService } from 'src/app/shared/services/lock-card/lock-card.service';
import { LockCardModel } from 'src/app/shared/models/lock-card.model';
import { mergeMap } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { DispenserService } from 'src/app/shared/services/dispenser/dispenser.service';
import { UtilityService } from 'src/app/shared/services/utility/utility.service';
import { HubWalkinService } from 'src/app/shared/services/hub-walkin/hub-walkin.service';
import { TranslateService } from '@ngx-translate/core';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-booking-create',
  templateUrl: './booking-create.component.html',
  styleUrls: ['./booking-create.component.scss'],
})
export class BookingCreateComponent implements OnInit, OnDestroy {
  // @Input() bookingData: any;
  // @Input() totalRateData: any;
  @Output() nextStep: EventEmitter<any> = new EventEmitter();
  @Output() backStep: EventEmitter<any> = new EventEmitter();

  public isLoading = {
    create: false,
  };
  public totalRateData: any = {};
  public bookingData: any = {};

  private hubSubcription;

  constructor(
    private bookingService: BookingService,
    private hubService: HubService,
    private lockCardService: LockCardService,
    private dispenserService: DispenserService,
    private hubWalkin: HubWalkinService,
    public utilService: UtilityService,
    private translateService: TranslateService,
    private router: Router
  ) {
    this.hubSubcription = this.hubService.onHub().subscribe((res: HubModel) => {
      if (res.receiver !== PAGES.bookingCreate) {
        return;
      }
      if (res.message === ACTIONS.nextStep) {
        this.createBooking();
      } else if (res.message === ACTIONS.backStep) {
        this.changeToPreviousStep();
      }
    });
  }

  ngOnInit(): void {
    this.hubWalkin.setStep(3);
    this.bookingData = { ...this.hubWalkin.bookingData };
    this.hubService.setLoading(true);
    this.bookingService
      .getTotalRate({
        ArrivalDate: this.bookingData.arrivalDate,
        DepartDate: this.bookingData.departDate,
        ArrivalTime: this.bookingData.arrivalTime,
        DepartTime: this.bookingData.departTime,
        RoomBocked: this.bookingData.roomCode,
        RoomType: this.bookingData.roomTypeCode,
        NoOfAdults: this.bookingData.adult,
        NoOfChild: this.bookingData.child,
      })
      .subscribe(
        (res: ResponseBody) => {
          if (res.metadata && res.metadata[0]) {
            this.totalRateData = res.metadata[0];
          } else {
            this.hubService.error(
              this.translateService.instant('msg.errorGetTotalRate')
            );
          }
        },
        (err) => {
          this.hubService.error(
            this.translateService.instant('msg.errorGetTotalRate')
          );
        }
      )
      .add(() => {
        this.hubService.setLoading(false);
      });
  }

  ngOnDestroy(): void {
    this.hubSubcription.unsubscribe();
  }

  onSubmit(): void {
    this.changeToBookingDetails();
  }

  changeToBookingDetails(): void {
    this.nextStep.emit({ isDone: true });
  }

  changeToPreviousStep(): void {
    this.backStep.emit();
  }

  createBooking() {
    console.log('BOOKING-CREATE: ', this.bookingData);
    this.hubService.setLoading(true);
    if (!environment.production) {
      this.bookingService
        .createBooking(this.bookingData)
        .subscribe(
          (res: any) => {
            if (res.code === 0) {
              this.hubWalkin.isDoneFlow = true;
              this.nextStep.emit({ isDone: true });
              this.router.navigate([`/${PAGES.walkin}/${STEPS.step4}`]);
            } else {
              // this.nextStep.emit({ isDone: false, msg: res.message });
              this.hubWalkin.errorMessageCreateBooking = res.message;
              this.hubWalkin.isDoneFlow = false;
              this.router.navigate([`/${PAGES.walkin}/${STEPS.step4}`]);
              // this.hubService.error(res.message);
            }
          },
          ({ code, message }) => {
            // this.nextStep.emit({ isDone: false, msg: message });
            this.hubWalkin.errorMessageCreateBooking = message;
            this.hubWalkin.isDoneFlow = false;
            this.router.navigate([`/${PAGES.walkin}/${STEPS.step4}`]);
            // this.hubService.error(message);
          }
        )
        .add(() => {
          this.hubService.setLoading(false);
        });
      return;
    }
    this.bookingService
      .createBooking(this.bookingData)
      .pipe(
        mergeMap((res: ResponseBody) => {
          if (res.statusCode === 200 && res.metadata) {
            return this.dispenserService.moveToRfPos();
          } else {
            return throwError({ code: res.statusCode, message: res.message });
          }
        }),
        mergeMap((res: any) => {
          if (res.code === 0) {
            return this.writeCard();
          } else {
            return throwError({ code: res.code, message: res.message });
          }
        }),
        mergeMap((res: any) => {
          if (res.code === 0) {
            return this.dispenserService.eject();
          } else {
            return throwError({ code: res.code, message: res.message });
          }
        })
      )
      .subscribe(
        (res: any) => {
          if (res.code === 0) {
            this.hubWalkin.isDoneFlow = true;
            this.nextStep.emit({ isDone: true });
            this.router.navigate([`/${PAGES.walkin}/${STEPS.step4}`]);
          } else {
            // this.nextStep.emit({ isDone: false, msg: res.message });
            this.hubWalkin.errorMessageCreateBooking = res.message;
            this.hubWalkin.isDoneFlow = false;
            this.router.navigate([`/${PAGES.walkin}/${STEPS.step4}`]);
            // this.hubService.error(res.message);
          }
        },
        ({ code, message }) => {
          // this.nextStep.emit({ isDone: false, msg: message });
          this.hubWalkin.errorMessageCreateBooking = message;
          this.hubWalkin.isDoneFlow = false;
          this.router.navigate([`/${PAGES.walkin}/${STEPS.step4}`]);
          // this.hubService.error(message);
        }
      )
      .add(() => {
        this.hubService.setLoading(false);
      });
  }

  writeCard() {
    const lockArrDate = moment(this.bookingData.arrivalDate).format(
      'yyyy-MM-DD'
    );
    const lockDeptDate = moment(this.bookingData.departDate).format(
      'yyyy-MM-DD'
    );

    const lockArrTime = moment(this.bookingData.arrivalTime, 'HH:mm').format(
      'HH:mm:ss'
    );
    const lockDeptTime = moment(this.bookingData.departTime, 'HH:mm').format(
      'HH:mm:ss'
    );

    const body: LockCardModel = {
      room: this.bookingData.roomCode,
      building: this.bookingData.buildingCode,
      arrival: `${lockArrDate} ${lockArrTime}`,
      departure: `${lockDeptDate} ${lockDeptTime}`,
    };

    return this.lockCardService.writeCard(body);
  }

  /**
   * DISPLAY
   */
  displayArrival(): string {
    return moment(this.bookingData.arrivalDate).format('DD/MM/yyyy') || '';
  }

  displayDepart(): string {
    return moment(this.bookingData.departDate).format('DD/MM/yyyy') || '';
  }

  displayNumNight(): string {
    if (this.bookingData.arrivalDate === this.bookingData.departDate) {
      return `1`;
    }
    const start = moment(this.bookingData.arrivalDate);
    const end = moment(this.bookingData.departDate);
    return `${end.diff(start, 'd')}`;
  }

  displayTotal() {
    const total =
      this.totalRateData.rate +
      this.totalRateData.tax1 +
      this.totalRateData.tax2;
    return total || 0;
  }
}
